import React, {useRef, useState} from "react";
import logo from './conference/logo.png';
import {useHistory} from "react-router-dom";
import {useForm} from "react-hook-form";
import {If} from "react-if";
import {db} from "./firebase";
import {Helmet} from "react-helmet";
import userPhoto from "./userPhoto.png";

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const SignUp = () => {

    const [emailExists, setEmailExists] = useState(false);
    const [dob, setDob] = useState(null);

    const history = useHistory();
    const {register, errors, watch, handleSubmit} = useForm();

    const password = useRef({});
    password.current = watch("password", "");

    const onSubmit = data => {

        const user = {
            email: data.email,
            name: data.firstName.concat(' ', data.lastName),
            photo: userPhoto,
            type: 'Normal',
            dob: dob
        };

        insertIntoDB(user, data.password);
    };

    const insertIntoDB = (user, password) => {

        db.collection("users")
            .where('email', '==', user.email)
            .where('type', '==', user.type)
            .get().then(querySnapshot => {

            if (querySnapshot.size !== 0) {
                setEmailExists(true);
                setTimeout(() => setEmailExists(false), 3000);
            } else {
                db.collection("users").add({
                    ...user, created: new Date(), password
                }).then((doc) => {
                    user.id = doc.id;
                    localStorage.setItem("user", JSON.stringify(user));
                    history.push('/');
                    console.log(user);

                }).catch(error => console.error("Error adding document: ", error));
            }

        }).catch(error => console.log("Error getting documents: ", error));
    };

    return <div className="container-full bg-white">
        <Helmet>
            <title>Sign Up</title>
        </Helmet>
        {/*====================================================================================================
      Section 1 Grod view
      ========================================================================================================*/}
        <header
            className="MuiPaper-root MuiAppBar-root MuiAppBar-positionFixed MuiAppBar-colorPrimary MuiPaper-elevation4"
            style={{backgroundImage: 'linear-gradient(90deg, rgb(255, 115, 107), rgb(255, 34, 96))', height: 25}}>
            <div className="MuiToolbar-root MuiToolbar-regular MuiToolbar-gutters"/>
        </header>

        <If condition={Object.keys(errors).length !== 0}>
            <div id="toast-container" className="toast-top-right" style={{top:'97px'}}
                 aria-live="polite"
                 role="alert">
                <div className="toast toast-error">
                    <div className="toast-message">
                        {errors.firstName && (<div>{errors.firstName.message}</div>)}
                        {errors.email && (<div>{errors.email.message}</div>)}
                        {errors.password && (<div>{errors.password.message}</div>)}
                        {errors.confirmPassword && (<div>{errors.confirmPassword.message}</div>)}
                    </div>
                </div>
            </div>
        </If>

        <If condition={emailExists}>
            <div id="toast-container" className="toast-top-right" style={{top:'97px'}}
                 aria-live="polite"
                 role="alert">
                <div className="toast toast-error">
                    <div className="toast-message">
                        Email Already Exists
                    </div>
                </div>
            </div>
        </If>

        <nav className=" navbar navbar-expand-lg navbar-light bg-white overright-nabbar  border-bottom">
            <div className="container">
                <button className="navbar-toggler" type="button" data-toggle="collapse"
                        data-target="#navbarTogglerDemo03" aria-controls="navbarTogglerDemo03" aria-expanded="false"
                        aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"/>
                </button>
                <a className="navbar-brand" href="" onClick={event => event.preventDefault()}>
                    <img src={logo} style={{height: 44}} alt="logo"/>
                </a>
                <div className="collapse navbar-collapse" id="navbarTogglerDemo03">
                    <ul className="nav navbar-nav ml-auto mt-2 mt-lg-0">
                        <li className="nav-item c-text ">
                            <a className="nav-link c-text " onClick={() => history.push("/join")}>Join a Meeting</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link c-text " href="#">Host a Meeting</a>
                        </li>
                        <li className="nav-item">
                            <a type="button" onClick={() => history.push("/signin")}
                               className="mdc-button mdc-button--raised shado-baground-textC radius10-font"
                               style={{width: '100%', padding: '1px 32px'}}>Sign In</a>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>

        <div className="row">
            {/* Default form login */}
            <form className="border border-light p-5 margin col-8 col-sm-6 col-md-6  col-lg-4 mb-5"
                  style={{marginTop: '5%'}} onSubmit={handleSubmit(onSubmit)}>
                <p className="h4 mb-4 text-center">Sign Up</p>

                <div className="form-row">
                    <div className="form-group col-md-6">
                        <label htmlFor="inputFastName">First Name</label>
                        <input type="text" className="form-control" id="inputFastName"
                               placeholder="First Name" name="firstName"
                               ref={register({
                                   required: {
                                       value: true,
                                       message: "First Name Required"
                                   }
                               })}/>
                    </div>
                    <div className="form-group col-md-6">
                        <label htmlFor="inputLastName">Last Name</label>
                        <input type="text" className="form-control"
                               name="lastName" id="inputLastName" placeholder="Last Name"
                               ref={register}/>
                    </div>
                </div>
                <div className="form-row">
                    <div className="form-group col-md-6">
                        <label htmlFor="inputEmail4">Email Address</label>
                        <input type="text" className="form-control" id="inputEmail4"
                               placeholder="demo@gmail.com" name="email"
                               ref={register({
                                   required: {
                                       value: true,
                                       message: "Email Required"
                                   },
                                   pattern: {
                                       value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                       message: "Entered value does not match email format"
                                   }
                               })}
                        />
                    </div>
                    <div className="form-group col-md-6">
                        <label htmlFor="inputDateofbirth">Date of Birth</label>
                        {/*<input type="date" className="form-control" id="inputDateofbirth"
                               name="dob" ref={register}
                        />*/}
                        <DatePicker
                            showMonthDropdown
                            showYearDropdown
                            isClearable
                            dropdownMode="select"
                            className="form-control"
                            placeholderText="dd/mm/yyyy"
                            maxDate={new Date()}
                            dateFormat="dd/MM/yyyy"
                            selected={dob}
                            onChange={date => setDob(date)}
                        />

                    </div>
                </div>
                <div className="form-row">
                    <div className="form-group col-md-6">
                        <label htmlFor="Password">Password</label>
                        <input type="password" className="form-control" id="Password"
                               name="password" placeholder="********"
                               ref={register({
                                   required: {
                                       value: true,
                                       message: "Password Required"
                                   },
                                   minLength: {
                                       value: 4,
                                       message: "Password too Short"
                                   }
                               })}
                        />
                    </div>
                    <div className="form-group col-md-6">
                        <label htmlFor="confirm_password">Confirm Password</label>
                        <input type="password" className="form-control" id="confirm_password"
                               placeholder="*******" name="confirmPassword"
                               ref={register({
                                   required: {
                                       value: true,
                                       message: "Confirm Password Required"
                                   },
                                   validate: {
                                       validate: value => value === password.current || "The passwords do not match"
                                   }
                               })}
                        />
                    </div>
                </div>
                {/* Sign in button */}
                <div className="text-center col-sm-12">
                    <div className="row">
                        <button type="submit" className="mdc-button mdc-button--raised shado-baground-textC mt-2 btn-lg"
                                style={{width: '100%', marginTop: '20px 20px !important'}}><span
                            className="mdc-button__label">Sign Up</span></button>
                        {/* Button */}
                    </div>
                    <div className="custom-control custom-checkbox mb-5">
                        <label>Already have an account?&nbsp;
                            <a onClick={() => history.push("/signin")} className="c-text">Sign in</a>
                        </label>
                    </div>

                    <p className="mt-5">
                        By signing in, I agree to the
                        <a href="https://unlockworld.net/terms-conditions/" className="c-text"> Privacy
                        Policy</a> and
                        <a href="https://unlockworld.net/privacy-policy/" className="c-text"> Terms of Service</a>
                    </p>
                </div>
            </form>
            {/* Default form login */}
        </div>

    </div>;
};

export default SignUp;
