import { combineReducers } from 'redux';
import room from './room';
import me from './me';
import producers from './producers';
import dataProducers from './dataProducers';
import peers from './peers';
import consumers from './consumers';
import dataConsumers from './dataConsumers';
import notifications from './notifications';
import displayMode from './displayMode';
import textMessaging from "./textMessaging";
import rightSideMode from "./rightSideMode";
import meetingLoader from "./meetingLoader";
import recordingMessage from "./recordingMessage";
import recordingBarUtil from "./recordingBarUtil";

const reducers = combineReducers(
	{
		room,
		me,
		producers,
		dataProducers,
		peers,
		consumers,
		dataConsumers,
		notifications,
        displayMode,
        textMessaging,
        rightSideMode,
		meetingLoader,
		recordingMessage,
		recordingBarUtil
	});

export default reducers;
