import React from "react";
import { useHistory } from 'react-router-dom';

const EndMeeting = () => {

    let history = useHistory();
    const redirect = () => history.push('/home');

    return <div id="unlockMeetingEndId" style={{textAlign: "center", display: 'none'}}>
        <button
            className="btn-floating btn btn-danger btn-sm shado-baground-textC"
            style={{height: "80px", fontSize: "18px"}}
            onClick={redirect}>Meeting is closed by host <br/>Click here to go back
        </button>
    </div>;
};

export default EndMeeting;