const initialState = {
    mode: 'grid',
    screenId: null
};

const displayMode = (state = initialState, action) =>
{
    switch (action.type)
    {
        case 'SET_DISPLAY_MODE':
        {
            const {mode, screenId} = action.payload;
            return {...state, mode, screenId};
        }

        default:
            return state;
    }
};

export default displayMode;