import domready from 'domready';
import React from 'react';
import {render} from 'react-dom';
import {Provider} from 'react-redux';
import {applyMiddleware as applyReduxMiddleware, createStore as createReduxStore} from 'redux';
import thunk from 'redux-thunk';
import Logger from './conference/Logger';
import RoomClient from './conference/RoomClient';
import reducers from './conference/redux/reducers';
// import {BrowserRouter as Router, Route} from "react-router-dom";
import {HashRouter as Router, Route} from "react-router-dom";
import Home from "./Home";
import PreRoom from "./conference/PreRoom";
import SignIn from "./SignIn";
import SignUp from "./SignUp";
import JoinMeeting from "./JoinMeeting";
import PrivateRoute from "./PrivateRoute";
import ForgotPassword from "./ForgotPassword";
import Schedule from "./Schedule";

const logger = new Logger();
const reduxMiddlewares = [ thunk ];

const store = createReduxStore(
	reducers,
	undefined,
	applyReduxMiddleware(...reduxMiddlewares)
);

RoomClient.init({ store });

domready(async () =>
{
	logger.debug('DOM ready');

	run();
});

async function run()
{
	render(
		<Provider store={store}>
			<Router>
				<PrivateRoute exact path={['/', '/home']} component={Home}/>
				<PrivateRoute path='/room/:rid' component={PreRoom}/>
				<Route path='/join' component={JoinMeeting}/>
				<Route path='/signin' component={SignIn}/>
				<Route path='/signup' component={SignUp}/>
				<Route path='/forgotPassword' component={ForgotPassword}/>
				<PrivateRoute path='/schedule' component={Schedule}/>
			</Router>
		</Provider>,
		document.getElementById('myDiv')
	);
}
