import React from "react";
import ScreenView from "./screen/ScreenView";
import {connect} from "react-redux";
import {withRoomContext} from "../RoomContext";
import {If} from "react-if";
import AllAudio from "./AllAudio";
import GridMain from "./grid/GridMain";
import SliderMain from "./slider/SliderMain";

const Main = (props) => {

    const {displayMode, peers} = props;

    return <main>
        <If condition={displayMode.mode === 'grid'}>
            <GridMain/>
        </If>
        <If condition={displayMode.mode === 'speaker'}>
            <SliderMain/>
        </If>
        <If condition={displayMode.mode === 'screen'}>
            <ScreenView/>
        </If>
        {peers.map((peer) => <AllAudio key={peer.id} id={peer.id}/>)}
    </main>;

};


const mapStateToProps = (state) =>
{
    const peersArray = Object.values(state.peers);

    return {
        displayMode: state.displayMode,
        peers: peersArray
    };
};


const MainContainer = withRoomContext(connect(
    mapStateToProps,
    null
)(Main));


export default MainContainer;
