import React, {useState} from 'react';
import logo from './conference/logo.png';
import {useHistory} from "react-router-dom";
import randomString from "random-string";
import {Helmet} from "react-helmet";
import {db} from "./firebase";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import classnames from "classnames";
import {If} from "react-if";
import clipboardCopy from "clipboard-copy";
import {Loader} from "react-loaders";
import BlockUi from "react-block-ui";
import 'react-block-ui/style.css';
import 'loaders.css/loaders.min.css';

import {RangeDatePicker} from 'react-google-flight-datepicker';
import 'react-google-flight-datepicker/dist/main.css';
import './schedule.css';

const Schedule = () => {

    const history = useHistory();

    const [user, setUser] = useState({});
    const [error, setError] = React.useState("none");
    const [info, setInfo] = React.useState("none");

    const [topic, setTopic] = useState("");
    const [newMeetingId, setNewMeetingId] = useState("");
    const [scheduleDate, setScheduleDate] = useState(new Date());
    const [scheduleTime, setScheduleTime] = useState(new Date());
    const [scheduleDocId, setScheduleDocId] = useState(null);

    const [scheduleList, setScheduleList] = React.useState([]);
    const [scheduleListStartTime, setScheduleListStartTime] = React.useState(new Date(1900, 0, 1).getTime());
    const [scheduleListEndTime, setScheduleListEndTime] = React.useState(new Date(2100, 0, 1).getTime());

    const [scheduleListPrevious, setScheduleListPrevious] = React.useState([]);
    const [scheduleListPreviousStartTime, setScheduleListPreviousStartTime] = React.useState(new Date(1900, 0, 1).getTime());
    const [scheduleListPreviousEndTime, setScheduleListPreviousEndTime] = React.useState(new Date(2100, 0, 1).getTime());

    const [refresh, setRefresh] = React.useState(new Date());

    const [scheduleListLoader, setScheduleListLoader] = React.useState(true);
    const [scheduleListPreviousLoader, setScheduleListPreviousLoader] = React.useState(true);

    React.useEffect(() => {
        const user = JSON.parse(localStorage.getItem("user"));
        setUser(user);

    }, []);

    React.useEffect(() => {
        const userId = JSON.parse(localStorage.getItem("user")).id;

        let lst = [];
        db.collection("schedule")
            .where('status', '==', 'CREATED')
            .where('userId', '==', userId)
            .get()
            .then(querySnapshot => querySnapshot.forEach(doc => lst.push({...doc.data(), sid: doc.id})))
            .then(() => {

                let filterLst = lst.filter(f =>
                    f.scheduleAt.toDate().getTime() > scheduleListStartTime &&
                    f.scheduleAt.toDate().getTime() < scheduleListEndTime
                ).sort((a, b) => (a.scheduleAt > b.scheduleAt) ? 1 : -1);

                setScheduleList(filterLst);
                setScheduleListLoader(false);

            })
            .catch(error => console.log("Error getting documents: ", error));

        let lstPrevious = [];
        db.collection("schedule")
            .where('status', '==', 'STARTED')
            .where('userId', '==', userId)
            .get()
            .then(querySnapshot => querySnapshot.forEach(doc => lstPrevious.push({...doc.data(), sid: doc.id})))
            .then(() => {

                let filterLstPrevious = lstPrevious.filter(f =>
                    f.scheduleAt.toDate().getTime() > scheduleListPreviousStartTime &&
                    f.scheduleAt.toDate().getTime() < scheduleListPreviousEndTime
                ).sort((a, b) => (a.scheduleAt > b.scheduleAt) ? 1 : -1);

                setScheduleListPrevious(filterLstPrevious);
                setScheduleListPreviousLoader(false);

            })
            .catch(error => console.log("Error getting documents: ", error));

    }, [refresh,
        scheduleListStartTime, scheduleListEndTime,
        scheduleListPreviousStartTime, scheduleListPreviousEndTime]
    );

    const joinMeeting = () => history.push("/join");

    const homePage = () => history.push("/");

    const schedule = () => history.push("/schedule");

    const hostMeeting = () => {
        const meetingId = randomString({length: 8}).toLowerCase();

        db.collection("meeting").add({
            meetingId,
            userId: user.id,
            created: new Date()
        }).then(doc => {
            history.push('/room/' + meetingId);
        }).catch(error => console.error("Error adding document: ", error));

    };

    const personalMeeting = () => {
        const meetingId = user.id;

        db.collection("meeting").add({
            meetingId,
            userId: user.id,
            created: new Date()
        }).then(doc => {
            history.push('/room/' + meetingId);
        }).catch(error => console.error("Error adding document: ", error));
    };

    const copyInvitation = ({topic, scheduleAt, meetingId}) => {

        let time = scheduleAt.toDate().toLocaleString('default', {weekday: 'long'}) + ', ' +
            scheduleAt.toDate().toLocaleString('default', {month: 'long'}) + ' ' +
            scheduleAt.toDate().getDate() + ', ' +
            scheduleAt.toDate().getHours() + ':';
        time += scheduleAt.toDate().getMinutes() < 10 ? '00' : scheduleAt.toDate().getMinutes();

        const copyRoomUrl = 'Unlock World\nMake Your Life Easier with Unlock!\n' +
            'https://meeting.unlockworld.net' + '/room/' + meetingId + '\nRoom ID: ' + meetingId + '\n\n' +
            'Topic: ' + topic + '\n' +
            'Time: ' + time;

        clipboardCopy(copyRoomUrl).then(() => {
            setInfo("block");
            setTimeout(() => setInfo("none"), 3000);
        });
    };

    const copyPersonalInvitation = meetingId => {

        const copyRoomUrl = 'Unlock World\nMake Your Life Easier with Unlock!\n' +
            'https://meeting.unlockworld.net' + '/room/' + meetingId + '\nRoom ID: ' + meetingId + '\n\n' +
            'Topic: ' + user.name + '’s Personal Meeting Room' + '\n' +
            'Time: ' + new Date();

        clipboardCopy(copyRoomUrl).then(() => {
            setInfo("block");
            setTimeout(() => setInfo("none"), 3000);
        });
    };


    const logout = () => {
        localStorage.removeItem('user');
        history.push("/signin");
    };

    const createScheduleBtnClk = () => {
        setTopic("My Meeting");
        setNewMeetingId(randomString({length: 9}).toLowerCase());
        setScheduleDate(new Date());
        setScheduleDocId(null);

        const st = new Date();
        const minutes = st.getMinutes();
        const mod = (minutes % 15);
        st.setMinutes( minutes + 15 - mod );
        setScheduleTime(st);

    };

    const saveSchedule = () => {
        if (!Boolean(topic) || !Boolean(scheduleDate) || !Boolean(scheduleTime)) {
            setError("block");
            setTimeout(() => setError("none"), 3000);
            return;
        }
        setScheduleListLoader(true);
        setScheduleListPreviousLoader(true);

        const scheduleAt = new Date(scheduleDate);
        scheduleAt.setHours(scheduleTime.getHours());
        scheduleAt.setMinutes(scheduleTime.getMinutes());

        const schedule = {
            topic,
            scheduleAt,
            meetingId: newMeetingId,
            userId: user.id,
            status: 'CREATED'
        };

        if (Boolean(scheduleDocId)) {
            db.collection('schedule').doc(scheduleDocId).update(schedule)
                .then(doc => setRefresh(new Date()))
                .catch(error => console.error("Error adding document: ", error));
        } else {
            db.collection("schedule").add(schedule)
                .then(doc => setRefresh(new Date()))
                .catch(error => console.error("Error adding document: ", error));
        }

    };

    const deleteSchedule = sid => {
        setScheduleListLoader(true);
        setScheduleListPreviousLoader(true);

        return db.collection("schedule").doc(sid).delete()
            .then(() => setRefresh(new Date()))
            .catch(error => console.error("Error removing document: ", error));
    };

    const editSchedule = ({topic, meetingId, scheduleAt, sid}) => {
        setTopic(topic);
        setNewMeetingId(meetingId);
        setScheduleDate(scheduleAt.toDate());
        setScheduleTime(scheduleAt.toDate());
        setScheduleDocId(sid);
    };

    const startSchedule = ({meetingId, sid}) => {

        db.collection('schedule').doc(sid)
            .update({status: 'STARTED'})
            .catch(error => console.error("Error adding document: ", error));

        db.collection("meeting").add({
            meetingId,
            userId: user.id,
            created: new Date()
        }).then(doc => history.push('/room/' + meetingId))
            .catch(error => console.error("Error adding document: ", error));
    };

    const loaderStyle = {position: "fixed", top: "50%", left: "50%", transform: "translate(-50%, -50%)"};

    return <>
        <div className="container-full bg-white">
            <Helmet>
                <title>Schedule</title>
            </Helmet>
            {/*====================================================================================================
      Section 1 Grod view
      ========================================================================================================*/}
            <header
                className="MuiPaper-root MuiAppBar-root MuiAppBar-positionFixed MuiAppBar-colorPrimary MuiPaper-elevation4"
                style={{backgroundImage: 'linear-gradient(90deg, rgb(255, 115, 107), rgb(255, 34, 96))', height: 25}}>
                <div className="MuiToolbar-root MuiToolbar-regular MuiToolbar-gutters"/>
            </header>

            <div style={{display: error}} id="toast-container" className="toast-top-right"
                 aria-live="polite"
                 role="alert">
                <div className="toast toast-error">
                    <div className="toast-message">
                        Failed to create a schedule
                    </div>
                </div>
            </div>

            <div style={{display: info}} id="toast-container" className="toast-top-right"
                 aria-live="polite"
                 role="alert">
                <div className="toast toast-success">
                    <div className="toast-message">
                        Invitation copied to the clipboard
                    </div>
                </div>
            </div>

            <nav className=" navbar navbar-expand-lg navbar-light bg-white overright-nabbar  border-bottom">
                <a className="navbar-brand" href="" onClick={event => event.preventDefault()}>
                    <img src={logo} style={{height: 44}}/>
                </a>
                <button className="navbar-toggler" type="button" data-toggle="collapse"
                        data-target="#navbarTogglerDemo03"
                        aria-controls="navbarTogglerDemo03" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"/>
                </button>
                <div className="collapse navbar-collapse" id="navbarTogglerDemo03">
                    <ul className="nav navbar-nav ml-auto mt-2 mt-lg-0">
                        <li className="nav-item c-text ">
                            <a className="nav-link c-text " onClick={joinMeeting}>Join a Meeting</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link c-text " onClick={hostMeeting}>Host a Meeting</a>
                        </li>
                        <li className="nav-item">
                            <button className="mdc-button mdc-button--raised shado-baground-textC radius10-font"
                                    style={{width: '100%', padding: '1px 32px'}} onClick={logout}>Logout
                            </button>
                        </li>
                    </ul>
                </div>
            </nav>

            <a className="d-sm-block d-md-none " style={{
                position: 'absolute',
                zIndex: 999999,
                WebkitFontSmoothing: 'subpixel-antialiased',
                backgroundColor: '#f7023e',
                borderRadius: '49px 0 0 49px',
                padding: '1px   9px',
                color: '#fff',
                top: '18%',
                right: 0
            }}>
                <i className="fa fa-cogs" aria-hidden="true"/>
            </a>

            <div className="row">
                <nav id="sidebar" className="col-md-2 d-none d-md-block bg-common sidebar mobile-absolute">
                    <div className="sidebar-sticky">
                        <ul className="nav flex-column">
                            <li className="nav-item">
                                <a className="nav-link" href="#" onClick={event => {
                                    event.preventDefault();
                                    homePage();
                                }}> Profile</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link active" href="#" onClick={event => {
                                    event.preventDefault();
                                    schedule();
                                }}>Meetings</a>
                            </li>
                            {/*<li className="nav-item">
                            <a className="nav-link" href="#">Webinars</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">Recordings</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">Settings</a>
                        </li>
                        <li className="nav-item">
                        </li>*/}
                        </ul>
                    </div>
                </nav>

                <main role="main" className="col-md-8 col-lg-9 px-4 pt-5">
                    <div className="container">
                        <div className="row">
                            <h2>
                                Meetings
                            </h2>
                        </div>
                    </div>

                    <BlockUi
                        renderChildren={true}
                        tag="div"
                        blocking={scheduleListLoader && scheduleListPreviousLoader}
                        loader={<Loader style={loaderStyle} active type={"semi-circle-spin"} color="#FF5549"/>}
                    >

                        <div className="container border p-4">
                            <nav>
                                <div className="nav nav-tabs" id="nav-tab" role="tablist">
                                    <a className="nav-item nav-link active" id="nav-home-tab" data-toggle="tab"
                                       href="#nav-home"
                                       role="tab" aria-controls="nav-home" aria-selected="true">Upcoming</a>
                                    <a className="nav-item nav-link" id="nav-profile-tab" data-toggle="tab"
                                       href="#nav-profile"
                                       role="tab" aria-controls="nav-profile" aria-selected="false">Previous</a>
                                    <a className="nav-item nav-link" id="nav-contact-tab" data-toggle="tab"
                                       href="#nav-contact"
                                       role="tab" aria-controls="nav-contact" aria-selected="false">Personal Room</a>
                                </div>
                            </nav>

                            <div className="tab-content" id="nav-tabContent">

                                <div className="tab-pane fade show pt-4 active" id="nav-home" role="tabpanel"
                                     aria-labelledby="nav-home-tab">
                                    <div className="d-flex mb-5">
                                        <div className="mr-auto pt-2">
                                            {/*<span className="border label p-2">
                                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Start time
                                            <b style={{color: '#000'}}>&nbsp;&nbsp;to&nbsp;&nbsp;</b>
                                            End time&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                            </span>*/}

                                            <RangeDatePicker
                                                onChange={(startDate, endDate) => {

                                                    if (Boolean(startDate))
                                                        setScheduleListStartTime(startDate.getTime());
                                                    else
                                                        setScheduleListStartTime(new Date(1900, 0, 1).getTime());

                                                    if (Boolean(endDate)) {
                                                        endDate.setDate(endDate.getDate() + 1);
                                                        setScheduleListEndTime(endDate.getTime());
                                                    } else {
                                                        setScheduleListEndTime(new Date(2100, 0, 1).getTime());
                                                    }

                                                }}
                                                minDate={new Date(1900, 0, 1)}
                                                maxDate={new Date(2100, 0, 1)}
                                                monthFormat="MMM YYYY"
                                                startDatePlaceholder="Start Time"
                                                endDatePlaceholder="End Time"
                                                disabled={false}
                                                className="schedule-date-form"
                                                startWeekDay="sunday"
                                                highlightToday={true}
                                            />

                                        </div>
                                        <div className="pb-2">
                                            <button
                                                onClick={() => createScheduleBtnClk()}
                                                className="mdc-button mdc-button--raised shado-baground-textC radius10-font"
                                                data-toggle="modal"
                                                data-target="#exampleModal">Schedule a Meeting
                                            </button>
                                        </div>
                                    </div>

                                    {scheduleList.map((obj, i) => (
                                        <div key={i} className="d-flex meeting-schdule mb-20">
                                            <div className="col-sm-3 border p-3">
                                                {obj.scheduleAt.toDate().toLocaleString('default', {weekday: 'long'})},&nbsp;
                                                {obj.scheduleAt.toDate().toLocaleString('default', {month: 'long'})}&nbsp;
                                                {obj.scheduleAt.toDate().getDate()}
                                                <h4>
                                                    {obj.scheduleAt.toDate().getHours()}:
                                                    {obj.scheduleAt.toDate().getMinutes() < 10 ? '00' : obj.scheduleAt.toDate().getMinutes()}
                                                </h4>
                                            </div>
                                            <div
                                                className={classnames('col-sm-6 border p-3', i % 2 === 0 ? 'text-danger' : '')}>
                                                <h4 className={classnames(i % 2 === 0 ? 'text-danger' : '')}>{obj.topic}</h4>
                                                <p>Meeting ID: {obj.meetingId}</p>
                                            </div>
                                            <div className="col-sm-3 border p-3">
                                                <button type="button"
                                                        onClick={() => copyInvitation(obj)}
                                                        className="btn btn-sm btn-outline-primary act">
                                                    Copy Invitation
                                                </button>
                                                <button type="button"
                                                        onClick={() => startSchedule(obj)}
                                                        className="btn btn-sm btn-outline-primary">
                                                    Start
                                                </button>
                                                <button type="button"
                                                        onClick={() => editSchedule(obj)}
                                                        data-toggle="modal"
                                                        data-target="#exampleModal"
                                                        className="btn btn-sm btn-outline-primary">
                                                    Edit
                                                </button>
                                                <button type="button"
                                                        onClick={() => deleteSchedule(obj.sid)}
                                                        className="btn btn-sm btn-outline-primary">
                                                    Delete
                                                </button>
                                                <p className="small label mb-0">
                                                    <i className="fa fa-wpforms"/>
                                                    &nbsp;&nbsp;
                                                    Report to Unlock
                                                </p>
                                            </div>
                                        </div>
                                    ))}

                                    <If condition={scheduleList.length === 0}>
                                        <div className="col-12 text-center label "
                                             style={{paddingTop: '20%', marginBottom: '20%'}}>
                                            The user does not have any upcoming meetings,<br/>
                                            To schedule a new meeting click Schedule a Meeting.
                                        </div>
                                    </If>

                                </div>

                                <div className="tab-pane fade pt-4 " id="nav-profile" role="tabpanel"
                                     aria-labelledby="nav-profile-tab">
                                    <div className="d-flex mb-5">
                                        <div className="mr-auto pt-2">
                                            {/*<span className="border label p-2">
                                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Start time
                                            <b style={{color: '#000'}}>&nbsp;&nbsp;to&nbsp;&nbsp;</b>
                                            End time&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                            </span>*/}

                                            <RangeDatePicker
                                                onChange={(startDate, endDate) => {

                                                    if (Boolean(startDate))
                                                        setScheduleListPreviousStartTime(startDate.getTime());
                                                    else
                                                        setScheduleListPreviousStartTime(new Date(1900, 0, 1).getTime());

                                                    if (Boolean(endDate)) {
                                                        endDate.setDate(endDate.getDate() + 1);
                                                        setScheduleListPreviousEndTime(endDate.getTime());
                                                    } else {
                                                        setScheduleListPreviousEndTime(new Date(2100, 0, 1).getTime());
                                                    }

                                                }}
                                                minDate={new Date(1900, 0, 1)}
                                                maxDate={new Date(2100, 0, 1)}
                                                monthFormat="MMM YYYY"
                                                startDatePlaceholder="Start Time"
                                                endDatePlaceholder="End Time"
                                                disabled={false}
                                                className="schedule-date-form"
                                                startWeekDay="sunday"
                                                highlightToday={true}
                                            />

                                        </div>
                                        <div className="pb-2">
                                            {/*<button
                                            onClick={() => createScheduleBtnClk()}
                                            className="mdc-button mdc-button--raised shado-baground-textC radius10-font"
                                            data-toggle="modal"
                                            data-target="#exampleModal">Schedule a Meeting
                                        </button>*/}
                                        </div>
                                    </div>

                                    {scheduleListPrevious.map((obj, i) => (
                                        <div key={i} className="d-flex meeting-schdule mb-20">
                                            <div className="col-sm-3 border p-3">
                                                {obj.scheduleAt.toDate().toLocaleString('default', {weekday: 'long'})},&nbsp;
                                                {obj.scheduleAt.toDate().toLocaleString('default', {month: 'long'})}&nbsp;
                                                {obj.scheduleAt.toDate().getDate()}
                                                <h4>
                                                    {obj.scheduleAt.toDate().getHours()}:
                                                    {obj.scheduleAt.toDate().getMinutes() < 10 ? '00' : obj.scheduleAt.toDate().getMinutes()}
                                                </h4>
                                            </div>
                                            <div
                                                className={classnames('col-sm-6 border p-3', i % 2 === 0 ? 'text-danger' : '')}>
                                                <h4 className={classnames(i % 2 === 0 ? 'text-danger' : '')}>{obj.topic}</h4>
                                                <p>Meeting ID: {obj.meetingId}</p>
                                            </div>
                                            <div className="col-sm-3 border p-3">
                                                <button type="button"
                                                        onClick={() => copyInvitation(obj)}
                                                        className="btn btn-sm btn-outline-primary act">
                                                    Copy Invitation
                                                </button>
                                                <button type="button"
                                                        onClick={() => startSchedule(obj)}
                                                        disabled={obj.status === 'STARTED'}
                                                        className="btn btn-sm btn-outline-primary">
                                                    Start
                                                </button>
                                                <button type="button"
                                                        onClick={() => editSchedule(obj)}
                                                        data-toggle="modal"
                                                        data-target="#exampleModal"
                                                        disabled={obj.status === 'STARTED'}
                                                        className="btn btn-sm btn-outline-primary">
                                                    Edit
                                                </button>
                                                <button type="button"
                                                        onClick={() => deleteSchedule(obj.sid)}
                                                        className="btn btn-sm btn-outline-primary">
                                                    Delete
                                                </button>
                                                <p className="small label mb-0">
                                                    <i className="fa fa-wpforms"/>
                                                    &nbsp;&nbsp;
                                                    Report to Unlock
                                                </p>
                                            </div>
                                        </div>
                                    ))}

                                </div>

                                <div className="tab-pane fade pt-4 "
                                     id="nav-contact" role="tabpanel" aria-labelledby="nav-contact-tab">

                                    <div style={{height: '60vh'}}>
                                        <div className="row">
                                            <div className="col-3">Topic</div>
                                            <div className="col-9">{user.name}’s Personal Meeting Room</div>
                                        </div>
                                        <div className="row">
                                            <div className="col-3">Meeting ID</div>
                                            <div className="col-9">{user.id}</div>
                                        </div>
                                        <div className="row">
                                            <div className="col-3">Email</div>
                                            <div className="col-9">{user.email}</div>
                                        </div>
                                        <div className="row">
                                            <div className="col-3">Join URL:</div>
                                            <div className="col-9">{'https://meeting.unlockworld.net' + '/room/' + user.id}</div>
                                        </div>
                                        <div className="row pt-5 " id="personeal-sec1" style={{display: 'none'}}>
                                            <form>
                                                <div className="row">
                                                    <div className="form-group col-6">
                                                        <div className="col-sm-12">
                                                            <label
                                                                className="form-control-static detail-label">Topic</label>
                                                        </div>
                                                        <div id="display-name-form-group"
                                                             className="col-sm-12 form-group"
                                                             style={{margin: 0}}>
                                                            <input type="text" className="form-control"
                                                                   placeholder="Topic"/>
                                                            <div className="valid-feedback">Looks good!</div>
                                                        </div>
                                                    </div>
                                                    <div className="form-group col-6">
                                                        <div className="col-sm-12">
                                                            <label className="form-control-static detail-label">Meeting
                                                                ID
                                                            </label>
                                                        </div>
                                                        <div id="display-name-form-group"
                                                             className="col-sm-12 form-group"
                                                             style={{margin: 0}}>
                                                            <input type="text" className="form-control"
                                                                   placeholder="Display Name"/>
                                                            <div className="invalid-feedback"> Please provide a valid
                                                                city.
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="form-group col-6">
                                                        <div className="col-sm-12">
                                                            <label className="form-control-static detail-label">Security
                                                                Code
                                                            </label>
                                                        </div>
                                                        <div id="display-name-form-group"
                                                             className="col-sm-12 form-group"
                                                             style={{margin: 0}}>
                                                            <input type="text" className="form-control"
                                                                   placeholder="Full Name"/>
                                                            <div className="valid-feedback">Looks good!</div>
                                                        </div>
                                                    </div>
                                                    <div className="form-group col-6">
                                                        <div className="col-sm-12">
                                                            <label className="form-control-static detail-label">Join
                                                                URL</label>
                                                        </div>
                                                        <div id="display-name-form-group"
                                                             className="col-sm-12 form-group"
                                                             style={{margin: 0}}>
                                                            <input type="text" className="form-control"
                                                                   placeholder="Display Name"/>
                                                            <div className="invalid-feedback"> Please provide a valid
                                                                city.
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="form-group col-12 pl-4 m-2">
                                                        <button type="button"
                                                                className="btn shado-baground-textS btn-md">
                                                            Savs change
                                                        </button>
                                                        <button type="button"
                                                                className="btn shado-baground-textC btn-md">Cancel
                                                        </button>
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                        <div className="mt-5">
                                            <button type="button"
                                                    onClick={() => personalMeeting()}
                                                    className="btn shado-baground-textS btn-md">
                                                Start
                                            </button>
                                            <button
                                                type="button"
                                                onClick={() => copyPersonalInvitation(user.id)}
                                                className="btn btn-outline-secondary btn-md gray-button">
                                                <i className="fa fa-files-o"/>
                                                &nbsp;&nbsp;&nbsp;&nbsp;
                                                Copy the invitation
                                            </button>
                                            <button type="button"
                                                    onClick={() => homePage()}
                                                    className="btn shado-baground-textC btn-md">
                                                Cancel
                                            </button>
                                        </div>
                                    </div>
                                </div>

                            </div>

                        </div>
                    </BlockUi>
                </main>

            </div>

        </div>

        <div className="modal fade bd-example-modal-lg" id="exampleModal" tabIndex={-1} data-backdrop="static"
             aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content">
                    <div className="modal-header" style={{
                        backgroundImage: 'linear-gradient(90deg, rgb(255, 34, 96), rgb(255, 115, 107))',
                        color: '#fff',
                        paddingBottom: 20
                    }}>
                        <h5 className="modal-title" id="exampleModalLabel">Schedule Meeting</h5>{/* Button */}
                    </div>
                    <div id="daynamicbody" className="modal-body">
                        <div className="col-12">
                            <div className="row">
                                <div className="row pt-3 " id="personeal-sec1">
                                    <form id="add-schdule">
                                        <div className="row">
                                            <div className="form-group col-12">
                                                <div className="col-sm-12">
                                                    <label className="form-control-static detail-label">Topic</label>
                                                </div>
                                                <div id="display-name-form-group" className="col-sm-12 form-group"
                                                     style={{margin: 0}}>
                                                    <input type="text"
                                                           value={topic}
                                                           onChange={e => setTopic(e.target.value)}
                                                           className="form-control"
                                                           placeholder="My Meeting"/>
                                                    {/*<div className="invalid-feedback"> Please provide a valid city.
                                                    </div>*/}
                                                </div>

                                            </div>
                                            <div className="form-group col-8">
                                                <div className="col-sm-12">
                                                    <label className="form-control-static detail-label">When</label>
                                                </div>
                                                <div className="col-sm-12">
                                                    <div className="row">
                                                        <div id="display-name-form-group"
                                                             className="col-sm-7 form-group"
                                                             style={{margin: 0}}>
                                                            {/*<input type="time" className="form-control"
                                                                   id="birthdaytime" name="birthdaytime"/>*/}
                                                            <DatePicker
                                                                className="form-control"
                                                                placeholderText="Date"
                                                                dateFormat="dd/MM/yyyy"
                                                                minDate={new Date()}
                                                                selected={scheduleDate}
                                                                onChange={date => setScheduleDate(date)}/>

                                                        </div>
                                                        <div id="display-name-form-group"
                                                             className="col-sm-5 form-group"
                                                             style={{margin: 0}}>
                                                            {/*<input type="time" className="form-control"
                                                                   id="birthdaytime" name="birthdaytime"/>*/}
                                                            <DatePicker
                                                                className="form-control"
                                                                placeholderText="Time"
                                                                showTimeSelect
                                                                showTimeSelectOnly
                                                                timeIntervals={15}
                                                                timeCaption="Time"
                                                                dateFormat="h:mm aa"
                                                                selected={scheduleTime}
                                                                onChange={date => setScheduleTime(date)}
                                                            />
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                            <div className="form-group col-4">
                                                <div className="col-sm-12">
                                                    <label className="form-control-static detail-label">Meeting
                                                        ID</label>
                                                </div>
                                                <div id="display-name-form-group" className="col-sm-12 form-group"
                                                     style={{margin: '3px'}}>
                                                    {/*<input type="text" className="form-control" readOnly={true}
                                                           placeholder="Display Name"/>
                                                    <div className="invalid-feedback"> Please provide a valid city.
                                                    </div>*/}
                                                    {newMeetingId}
                                                </div>
                                            </div>

                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="modal-footer">
                        <hr/>
                        <button type="button"
                                data-dismiss="modal"
                                className="mdc-button mdc-button--raised shado-baground-textC mt-2"
                                onClick={() => saveSchedule()}>
                            Save change
                        </button>
                        <button type="button"
                                className="mdc-button mdc-button--raised shado-baground-textC mt-2"
                                data-dismiss="modal">Cancel
                        </button>
                    </div>
                </div>
            </div>
        </div>


    </>;


};


export default Schedule;