import React, {Component} from "react";
import logo from './conference/logo.png';
import {withRouter} from "react-router-dom";
import {Helmet} from "react-helmet";

class JoinMeeting extends Component {

    constructor(props) {
        super(props);
        this.state = {
            roomId: null,
            error: "none"
        };
    }

    mySubmitHandler = (event) => {
        event.preventDefault();
        let roomId = this.state.roomId;

        /*if (!Boolean(displayName)) {
            this.setState({error: "block"});
            setTimeout(() => { this.setState({error: "none"}) }, 3000);
            return;
        }*/

        if (!Boolean(roomId)) {
            this.setState({error: "block"});
            setTimeout(() => { this.setState({error: "none"}) }, 3000);
            return;
        }

        this.props.history.push('/room/' + roomId);
    };

    myChangeHandler = (event) => {
        this.setState({[event.target.name]: event.target.value});
    };

    homePage = () => this.props.history.push("/");

    render() {
        return (
            <div className="container-full bg-white" style={{height: '100vh'}}>
                <Helmet>
                    <title>Join</title>
                </Helmet>
                <header
                    className="MuiPaper-root MuiAppBar-root MuiAppBar-positionFixed MuiAppBar-colorPrimary MuiPaper-elevation4"
                    style={{
                        backgroundImage: 'linear-gradient(90deg, rgb(255, 115, 107), rgb(255, 34, 96))',
                        height: 25
                    }}>
                    <div className="MuiToolbar-root MuiToolbar-regular MuiToolbar-gutters"/>
                </header>

                <div style={{display: this.state.error}} id="toast-container" className="toast-top-right"
                     aria-live="polite"
                     role="alert">
                    <div className="toast toast-error">
                        <div className="toast-message">
                            Please Enter Meeting ID
                        </div>
                    </div>
                </div>

                <div className="row">

                    <form className="border border-light p-5 margin col-md-4" style={{marginTop: '10%'}}
                          onSubmit={this.mySubmitHandler}>
                        <p className="h4 mb-4 text-center"><img style={{width:"35%"}} src={logo}/></p>

                        {/*<div className="form-group">
                            <label htmlFor="idName">Enter Your Name</label>
                            <input type="text" id="idName" name='displayName' onChange={this.myChangeHandler}
                                   value={this.state.displayName || ''} className="form-control mb-6 border4-light "/>
                        </div>*/}

                        <div className="form-group">
                            <label htmlFor="meetingid">Enter Meeting ID</label>
                            <input type="text" id="meetingid" name='roomId' onChange={this.myChangeHandler}
                                   value={this.state.roomId || ''} className="form-control mb-6 border4-light"/>
                        </div>

                        <div className="text-center col-sm-12 mb-5">
                            <div className="row">
                                <button type="submit" className="mdc-button mdc-button--raised shado-baground-textC mt-2 btn-lg"
                                        style={{width: '100%'}}><span className="mdc-button__label"><i
                                    className="fa fa-users"></i> Join</span></button>
                            </div>

                        </div>
                        <i onClick={this.homePage} className="material-icons">arrow_back</i>
                    </form>

                </div>
            </div>

        );
    }
}

export default withRouter(JoinMeeting);