import React from "react";
import * as appPropTypes from "../appPropTypes";
import {connect} from "react-redux";
import PropTypes from "prop-types";
import * as requestActions from "../../redux/requestActions";
import {withRoomContext} from "../../RoomContext";

const SiderBarFooter = (props) => {

    const [message, setMessage] = React.useState("");
    const [receiver, setReceiver] = React.useState("0");
    const [members, setMembers] = React.useState([]);

    const {peers, invalidMessage, roomClient} = props;

    React.useEffect(() => {
        let map = peers.map(m => ({displayName: m.displayName, id: m.id}));
        setMembers(map);

    }, [peers]);


    const sendMessageEnter = e => {

        if (!Boolean(e.target.value)) {
            return false;
        }

        if (e.key === 'Enter') {

            const messagePayload = {message, receiver, time: new Date().getTime()};

            setMessage("");
            e.preventDefault();

            if (Number(receiver) === 0 || (!members.some(member => member.id === receiver) && Number(receiver) !== 1)) {
                invalidMessage();
                return;
            }

            const payload = {...messagePayload, group: Number(receiver) === 1};

            roomClient.meetingChat(payload);
        }
    };


    return (
        <div className="siderbarfooter text-center col border-top">
            <div className="row">

                <form
                    id="chatMessage"
                    style={{
                        width: "100%"
                    }}
                    name="chatMessage"
                >

                    <div className="col-sm-12">
                        <div className="row">
                            <div className="input-group p-1">
                                <label
                                    className="input-group-text c-text"
                                    htmlFor="inputGroupSelect01"
                                    style={{border: "unset", background: "unset"}}
                                >
                                    To:
                                </label>
                                <select
                                    defaultValue={'0'}
                                    className="custom-select selectCustom pd5"
                                    id="inputGroupSelect01"
                                    onChange={(e) => setReceiver(e.target.value)}
                                >
                                    <option value="0">select...</option>
                                    <option value="1">All Participants</option>
                                    {members.map(peer => <option key={peer.id}
                                                                 value={peer.id}>{peer.displayName}</option>)}
                                </select>
                            </div>
                        </div>
                    </div>

                    <div className="col-sm-12">
              <textarea
                  style={{
                      border: "0px"
                  }}
                  placeholder="Type Message Here..."
                  id="messageTypehere"
                  onKeyPress={(e) => sendMessageEnter(e)}
                  onChange={(e) => setMessage(e.target.value)}
                  value={message}
              />
                    </div>

                </form>

            </div>
        </div>
    );

};

SiderBarFooter.propTypes =
    {
        peers           : PropTypes.arrayOf(appPropTypes.Peer).isRequired,
        activeSpeakerId : PropTypes.string
    };

const mapStateToProps = (state) =>
{
    const peersArray = Object.values(state.peers);

    return {
        peers           : peersArray,
        activeSpeakerId : state.room.activeSpeakerId,
        invalidMessage  : PropTypes.func.isRequired
    };
};

const mapDispatchToProps = (dispatch) =>
{
    return {
        invalidMessage: () => {
            dispatch(requestActions.notify(
                {
                    type : 'error',
                    text: 'No participant selected'
                }));
        }
    };
};


const SiderBarFooterContainer = withRoomContext(connect(
    mapStateToProps,
    mapDispatchToProps
)(SiderBarFooter));


export default SiderBarFooterContainer;

