import React from 'react';
import logo from './conference/logo.png';
import {GoogleLogin} from 'react-google-login';
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';
import {useHistory, useLocation} from "react-router-dom";
import {Helmet} from "react-helmet";
import {db} from "./firebase";

const SignIn = () => {

    const history = useHistory();
    const location = useLocation();

    const [email, setEmail] = React.useState("");
    const [password, setPassword] = React.useState("");
    const [error, setError] = React.useState("none");
    const [inputType, setInputType] = React.useState("password");

    function upsertDB(user) {
        db.collection("users")
            .where('email', '==', user.email)
            .where('type', '==', user.type)
            .get().then(querySnapshot => {

            if (querySnapshot.empty) {
                db.collection("users").add({
                    ...user, created: new Date(), password: null
                }).then((doc) => {
                    user.id = doc.id;
                    localStorage.setItem("user", JSON.stringify(user));
                    history.push(Boolean(location.state) ? location.state.redirectPage : '/');
                }).catch(error => console.error("Error adding document: ", error));
            } else {
                querySnapshot.forEach(doc => {
                    const user = {...doc.data()};
                    user.id = doc.id;
                    delete user.created;
                    delete user.password;
                    localStorage.setItem("user", JSON.stringify(user));
                    history.push(Boolean(location.state) ? location.state.redirectPage : '/');
                });
            }

        }).catch(error => console.log("Error getting documents: ", error));
    }

    const responseGoogle = (response) => {
        let obj = response.profileObj;
        if (obj != null) {
            const {email, name, imageUrl} = obj;
            const user = {
                email,
                name,
                photo: imageUrl,
                type: 'Google'
            };
            upsertDB(user);
        }
    };

    const responseFacebook = (response) => {
        if (response.status == null) {
            const {email, name, picture} = response;
            const user = {
                email,
                name,
                photo: picture.data.url,
                type:'Facebook'
            };
            upsertDB(user);
        }
    };

    const responseNormal = e => {
        e.preventDefault();

        if (!Boolean(email) || !Boolean(password)) {
            setError("block");
            setTimeout(() => setError("none"), 3000);
            return;
        }

        db.collection("users")
            .where('email', '==', email)
            .where('password', '==', password)
            .where('type', '==', 'Normal')
            .get().then(querySnapshot => {

            if (!querySnapshot.empty) {
                querySnapshot.forEach(doc => {
                    const user = {...doc.data()};
                    user.id = doc.id;
                    delete user.created;
                    delete user.password;
                    console.log(user);

                    localStorage.setItem("user", JSON.stringify(user));
                    history.push(Boolean(location.state) ? location.state.redirectPage : '/');
                });
            } else {
                setError("block");
                setTimeout(() => setError("none"), 3000);
            }

        }).catch(error => console.log("Error getting documents: ", error));

    };

    return <div className="container-full bg-white">
        <Helmet>
            <title>Sign In</title>
        </Helmet>
        {/*====================================================================================================
      Section 1 Grod view
      ========================================================================================================*/}
        <header
            className="MuiPaper-root MuiAppBar-root MuiAppBar-positionFixed MuiAppBar-colorPrimary MuiPaper-elevation4"
            style={{backgroundImage: 'linear-gradient(90deg, rgb(255, 115, 107), rgb(255, 34, 96))', height: 25}}>
            <div className="MuiToolbar-root MuiToolbar-regular MuiToolbar-gutters"/>
        </header>

        <div style={{display: error}} id="toast-container" className="toast-top-right"
             aria-live="polite"
             role="alert">
            <div className="toast toast-error">
                <div className="toast-message">
                    Invalid Username or Password
                </div>
            </div>
        </div>

        <nav className=" navbar navbar-expand-lg navbar-light bg-white overright-nabbar  border-bottom">
            <div className="container">
                <button className="navbar-toggler" type="button" data-toggle="collapse"
                        data-target="#navbarTogglerDemo03" aria-controls="navbarTogglerDemo03" aria-expanded="false"
                        aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"/>
                </button>
                <a className="navbar-brand" href="" onClick={event => event.preventDefault()}>
                    <img src={logo} style={{height: 44}} alt="Logo"/>
                </a>
                <div className="collapse navbar-collapse" id="navbarTogglerDemo03">
                    <ul className="nav navbar-nav ml-auto mt-2 mt-lg-0">
                        <li className="nav-item c-text ">
                            <a className="nav-link c-text "
                               onClick={() => history.push("/join")}
                            >Join a Meeting</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link c-text ">Host a Meeting</a>
                        </li>
                        <li className="nav-item">
                            <a type="button" onClick={() => history.push("/signup")}
                               className="mdc-button mdc-button--raised shado-baground-textC radius10-font"
                               style={{width: '100%', padding: '1px 32px'}}>Sign Up</a>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
        <div className="row">
            {/* Default form login */}
            <form className="border border-light p-5 margin col-8 col-sm-6 col-md-6  col-lg-4 mb-5"
                  style={{marginTop: '5%', maxWidth: 400}}>
                <p className="h4 mb-4 text-center">Sign In</p>
                <div className="form-group">
                    <label htmlFor="email" className="text-left">Email</label>
                    <input type="text" id="email" name="email" maxLength={50} className="form-control"
                           onChange={(e) => setEmail(e.target.value)}/>
                </div>
                <div className="form-group position-relative">
                    <label htmlFor="inputAddress">Password</label>
                    <div className="position-relative">
                        <input id="password-field" type={inputType} className="form-control" name="password"
                               onChange={(e) => setPassword(e.target.value)}/>
                        <span toggle="#password-field"
                              onClick={() => setInputType(inputType === 'password' ? 'text' : 'password')}
                              className="fa fa-fw fa-eye field-icon toggle-password position-absolute mt-2"/>
                    </div>
                    {/* Forgot password */}
                    <a href="#" className="forgerpass c-text"
                       onClick={e => {
                           e.preventDefault();
                           history.push("/forgotPassword");
                       }}>Forgot password?</a>
                </div>
                {/* Sign in button */}
                <div className="text-center col-sm-12">
                    <div className="row">
                        <button
                            onClick={responseNormal}
                            className="mdc-button mdc-button--raised shado-baground-textC mt-2 btn-lg"
                            style={{width: '100%', marginTop: '20px 20px !important'}}><span
                            className="mdc-button__label"><i className="fa fa-user"/> &nbsp;Sign in</span></button>
                        {/* Button */}
                    </div>
                    <div className="row mt-5">
                        {/*<label className="text-left w-100 color">Sign in with</label>
                        <GoogleLogin
                            clientId="469850491209-vl4fr4p7c7jovtcunoofpn33fdj07tdr.apps.googleusercontent.com"
                            render={renderProps => (
                                <a href="#" onClick={renderProps.onClick} disabled={renderProps.disabled}
                                   className=" btn-50 google-login"><i className="fa fa-google"/>&nbsp;Google</a>
                            )}
                            buttonText="Login"
                            onSuccess={responseGoogle}
                            onFailure={responseGoogle}
                            cookiePolicy={'single_host_origin'}
                        />

                        <FacebookLogin
                            appId="887702965074565"
                            callback={responseFacebook}
                            fields="name,email,picture"
                            render={renderProps => (
                                <a onClick={renderProps.onClick} className="btn-50 facebook-login"><i
                                    className="fa fa-facebook"/>&nbsp;Facebook</a>
                            )}
                        />*/}
                    </div>
                </div>
            </form>
            {/* Default form login */}
        </div>
    </div>;
};


export default SignIn;