import React from "react";
import LeftPanelPart from "./LeftPanelPart";
import ChatSidebar from "./ChatSidebar";
import SidebarUserList from "./SidebarUserList";
import {connect} from "react-redux";
import {withRoomContext} from "../../RoomContext";
import {If} from "react-if";


const SliderView = (props) => {

    const {rightSideMode} = props;

    return <div id="sliderview">
        <div className="row">
            <LeftPanelPart/>
            <If condition={rightSideMode.mode === 'chat'}>
                <ChatSidebar/>
            </If>
            <If condition={rightSideMode.mode === 'list'}>
                <SidebarUserList/>
            </If>
        </div>
    </div>
};

const mapStateToProps = (state) =>
{
    return {
        rightSideMode            : state.rightSideMode
    };
};

const SliderViewContainer = withRoomContext(connect(
    mapStateToProps,
    null
)(SliderView));

export default SliderViewContainer;
