import React from "react";
import PeerGrid from "./PeerGrid";
import BottomMenu from "../BottomMenu";
import speakerView from '../img/speakerview.svg';
import gridView from '../img/grid_view.svg';
import {connect} from "react-redux";
import {If} from "react-if";
import Me from "./Me";
import * as stateActions from "../../redux/stateActions";
import Header from "../Header";

const GridMain = ({peers, changeDisplayMode}) => {

    const [count, setCount] = React.useState(1);
    const PER_PAGE = 11;

    const pagination = (items, current_page, per_page_items) => {
        let page = current_page || 1,
            per_page = per_page_items || 10,
            offset = (page - 1) * per_page,

            paginatedItems = items.slice(offset).slice(0, per_page_items),
            total_pages = Math.ceil(items.length / per_page);

        return {
            page: page,
            per_page: per_page,
            pre_page: page - 1 ? page - 1 : null,
            next_page: (total_pages > page) ? page + 1 : null,
            total: items.length,
            total_pages: total_pages,
            data: paginatedItems
        };
    };

    return (
        <main>
            <div id="clearfullview" className="container-fuid card clearfullview">
                <Header/>
                <div className="btn-group btn-group-sm top-right-button c-gray">
                    <a onClick={() => {
                        changeDisplayMode('speaker');
                    }} data-toggle="tooltip" data-placement="top" title="Speaker View">
                        <img src={speakerView}/></a>&nbsp;&nbsp;
                    <a onClick={() => {
                        changeDisplayMode('grid');
                    }} data-toggle="tooltip" data-placement="top" title="List View">
                        <img src={gridView}/></a>
                </div>

                <div className="col-lg-11 col-md-11 col-lx-12 margin"
                     style={{margin: 'auto', padding: '8px 5% 0px 5%'}}>
                    <div className="min-hidgrid">
                        <div id="galleryview" className="d-flex flex-wrap justify-content-center p-relative wrap">
                            <Me/>
                            {pagination(peers, count, PER_PAGE).data.map((peer) => <PeerGrid key={peer.id}
                                                                                             id={peer.id}/>)}
                        </div>
                    </div>

                    <BottomMenu/>

                    <If condition={Boolean(pagination(peers, count, PER_PAGE).pre_page)}>
                        <a className="left carousel-control shado-baground-textC" onClick={() => setCount(count - 1)}>
                            <i className="fa fa-angle-left" aria-hidden="true"/>
                        </a>
                    </If>
                    <If condition={Boolean(pagination(peers, count, PER_PAGE).next_page)}>
                        <a className="right carousel-control shado-baground-textC" onClick={() => setCount(count + 1)}>
                            <i className="fa fa-angle-right" aria-hidden="true"/>
                        </a>
                    </If>
                </div>
            </div>
        </main>
    );
};

const mapStateToProps = (state) =>
{
    const peersArray = Object.values(state.peers);

    return {
        peers           : peersArray
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        changeDisplayMode: (mode) => {
            dispatch(stateActions.changeDisplayMode(mode, null));
        }
    };
};

const GridMainContainer = connect(
    mapStateToProps,
    mapDispatchToProps)
(GridMain);



export default GridMainContainer;
