import React from "react";
import {withRoomContext} from "../../RoomContext";
import {connect} from "react-redux";
import {If} from "react-if";
import {db} from "../../../firebase";
import {useParams} from "react-router-dom";

const SidebarMember = props => {

    let { rid } = useParams();

    const {audioConsumer, videoConsumer, id, roomClient} = props;

    const audioEnabled = (
        Boolean(audioConsumer) &&
        !audioConsumer.locallyPaused &&
        !audioConsumer.remotelyPaused
    );

    const videoVisible = (
        Boolean(videoConsumer) &&
        !videoConsumer.locallyPaused &&
        !videoConsumer.remotelyPaused
    );

    const truncatedText = (str) => {
        let length = 23;
        let ending = '...';
        return str.length > length ? str.substring(0, length - ending.length) + ending : str;
    };

    function hostMuteUnmute(type, peerId) {
        const meetingId = rid;
        const userId = JSON.parse(localStorage.getItem("user")).id;

        db.collection("meeting")
            .where('userId', '==', userId)
            .where('meetingId', '==', meetingId)
            .get().then(querySnapshot => {

            if (querySnapshot.size !== 0)
                roomClient.controlOverAudioVideoByHost({type, peerId});

        }).catch(error => console.log("Error getting documents: ", error));
    }

    return <li>
        <a className="d-flex justify-content-between">
            <span className="material-icons">person_pin</span>
            <div className="w-75 ">
                <strong>{truncatedText(props.displayName)}</strong>
            </div>
            <div className="chat-footer">
                <If condition={!videoVisible}>
                    <i className="material-icons">videocam_off</i>
                </If>
                <If condition={videoVisible}>
                    <i className="material-icons"
                       onClick={() => hostMuteUnmute('VIDEO', id)}>videocam</i>
                </If>
                <If condition={!audioEnabled}>
                    <i className="material-icons">mic_off</i>
                </If>
                <If condition={audioEnabled}>
                    <i className="material-icons"
                       onClick={() => hostMuteUnmute('AUDIO', id)}>mic</i>
                </If>
            </div>
        </a>
    </li>;
};

const mapStateToProps = (state, {id}) => {
    const peer = state.peers[id];

    if (!Boolean(peer)) return {};

    const consumersArray = peer.consumers.map((consumerId) => state.consumers[consumerId]);
    const audioConsumer = consumersArray.find((consumer) => consumer.track.kind === 'audio');
    const videoConsumer = consumersArray.find((consumer) => consumer.track.kind === 'video');

    return {
        peer,
        audioConsumer,
        videoConsumer
    };
};

const SidebarMemberContainer = withRoomContext(connect(
    mapStateToProps,
    null
)(SidebarMember));

export default SidebarMemberContainer;
