import React from "react";
import {useParams} from "react-router-dom";
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemIcon from "@material-ui/core/ListItemIcon";
import AudiotrackIcon from '@material-ui/icons/Audiotrack';
import VideocamIcon from '@material-ui/icons/Videocam';
import {withRoomContext} from "../RoomContext";
import {connect} from "react-redux";
import * as stateActions from "../redux/stateActions";
import * as requestActions from "../redux/requestActions";

let recorder;

const BottomRecordingBar = (props) => {

    const {
        roomClient,
        startVisible,
        playVisible,
        pauseVisible,
        stopVisible,
        operateRecording,
        comingSoon
    } = props;

    let { rid } = useParams();

    const [playAnchorEl, setPlayAnchorEl] = React.useState(null);

    const start = async (onlyAudioRecording = true) => {
        setPlayAnchorEl(null);
        await recording(onlyAudioRecording);
        operateRecording('start', 'none');
        operateRecording('play', 'none');
        operateRecording('pause', 'inline');
        operateRecording('stop', 'inline');
    };

    const play = async () => {
        await recorder.resume();
        operateRecording('play','none');
        operateRecording('pause','inline');
    };

    const pause = async () => {
        await recorder.pause();
        operateRecording('pause', 'none');
        operateRecording('play', 'inline');
    };

    const stop = async () => {
        await recorder.stop();
        operateRecording('start', 'inline');
        operateRecording('play', 'none');
        operateRecording('pause', 'none');
        operateRecording('stop', 'none');
    };

    const download = (completeBlob, onlyAudioRecording) => {
        const url = URL.createObjectURL(completeBlob);
        const a = document.createElement('a');
        const extension = onlyAudioRecording ? '.mp3' : '.mp4';
        a.style.display = 'none';
        a.href = url;
        a.download = rid + extension;

        document.body.appendChild(a);
        a.click();
        setTimeout(() => {
            document.body.removeChild(a);
            window.URL.revokeObjectURL(url);
        }, 100);
    };

    const recording = async onlyAudioRecording => {

        let screen, audio, stream;

        if (onlyAudioRecording) {
            audio = await navigator.mediaDevices.getUserMedia({audio: true});
        } else {
            screen = await navigator.mediaDevices.getDisplayMedia({video: true});
            audio = await navigator.mediaDevices.getUserMedia({audio: true});
            stream = new MediaStream([audio.getTracks()[0], screen.getTracks()[0]]);
        }

        if (onlyAudioRecording)
            recorder = new MediaRecorder(audio);
        else
            recorder = new MediaRecorder(stream);

        const chunks = [];

        recorder.ondataavailable = e => chunks.push(e.data);

        recorder.onstop = e => {
            const completeBlob = new Blob(chunks, {type: chunks[0].type});

            if (onlyAudioRecording)
                audio.getTracks()[0].stop();
            else
                stream.getVideoTracks()[0].stop();

            roomClient.recordingStatus({message: ""});
            download(completeBlob, onlyAudioRecording);
        };

        recorder.onstart = e => roomClient.recordingStatus({message: "Recording..."});
        recorder.onpause = () => roomClient.recordingStatus({message: "Recording paused"});
        recorder.onresume = () => roomClient.recordingStatus({message: "Recording..."});
        recorder.start();
    };

    const playHandleClick = event => {
        // setPlayAnchorEl(event.currentTarget);
        comingSoon();
    };

    const handleClose = () => setPlayAnchorEl(null);

    return <>
        <button type="button"
                style={{display: startVisible}}
                onClick={playHandleClick}
                className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                data-toggle="tooltip"
                data-placement="top"
                title="Recording"
                data-original-title="Record">
            <i className="fa fa-dot-circle-o" aria-hidden="true"/>
        </button>

        <button type="button"
                style={{display: playVisible}}
                onClick={() => play()}
                className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                data-toggle="tooltip"
                data-placement="top"
                title="Play"
                data-original-title="Record">
            <i className="fa fa-play" aria-hidden="true"/>
        </button>

        <button type="button"
                style={{display: pauseVisible}}
                onClick={() => pause()}
                className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                data-toggle="tooltip"
                data-placement="top"
                title="Pause"
                data-original-title="Record">
            <i className="fa fa-pause" aria-hidden="true"/>
        </button>

        <button type="button"
                style={{display: stopVisible}}
                onClick={() => stop()}
                className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                data-toggle="tooltip"
                data-placement="top"
                title="Stop"
                data-original-title="Record">
            <i className="fa fa-stop" aria-hidden="true"/>
        </button>

        <Menu
            id="simple-menu"
            anchorEl={playAnchorEl}
            keepMounted
            open={Boolean(playAnchorEl)}
            onClose={handleClose}
        >
            <MenuItem onClick={() => start()} key={0}>
                <ListItemIcon>
                    <AudiotrackIcon fontSize="small"/>Audio
                </ListItemIcon>
            </MenuItem>
            <MenuItem onClick={() => start(false)} key={1}>
                <ListItemIcon>
                    <VideocamIcon fontSize="small"/>Screen
                </ListItemIcon>
            </MenuItem>
        </Menu>
    </>;
};

const mapStateToProps = (state) =>
{
    const recordingBarUtil = state.recordingBarUtil;

    return {
        startVisible: recordingBarUtil.start,
        playVisible: recordingBarUtil.play,
        pauseVisible: recordingBarUtil.pause,
        stopVisible: recordingBarUtil.stop
    };
};

const mapDispatchToProps = (dispatch) =>
{
    return {
        operateRecording: (key, value) => {
            dispatch(stateActions.operateRecording(key, value));
        },
        comingSoon: () => {
            dispatch(requestActions.notify(
                {
                    type: 'error',
                    text: 'Coming soon...'
                }));
        },
    };
};


const BottomRecordingBarContainer = withRoomContext(connect(
    mapStateToProps,
    mapDispatchToProps
)(BottomRecordingBar));

export default BottomRecordingBarContainer;
