import React from "react";
import {connect} from "react-redux";
import {If} from "react-if";
import {withRoomContext} from "../../RoomContext";
import {db} from "../../../firebase";
import {useParams} from "react-router-dom";

const SideBarUserFooter = ({audioEnabledCount, videoVisibleCount, roomClient}) => {

    let { rid } = useParams();

    const hostMuteUnmuteAll = type => {

        const meetingId = rid;
        const userId = JSON.parse(localStorage.getItem("user")).id;

        db.collection("meeting")
            .where('userId', '==', userId)
            .where('meetingId', '==', meetingId)
            .get().then(querySnapshot => {

            if (querySnapshot.size !== 0)
                roomClient.controlOverAudioVideoByHostAll({type});

        }).catch(error => console.log("Error getting documents: ", error));
    };

    return <div className="list-bottom-action w-100 text-center">

        <If condition={audioEnabledCount}>
            <button className="mdc-button shado-baground-nocolor radius10-font" disabled={true}>
                <span className="mdc-button__label">Unmute All</span>
            </button>
        </If>

        <If condition={!audioEnabledCount}>
            <button
                onClick={() => hostMuteUnmuteAll('AUDIO')}
                className="mdc-button mdc-button--raised shado-baground-textC radius10-font">
                <span className="mdc-button__label">Mute All</span>
            </button>
        </If>

        <If condition={videoVisibleCount}>
            <button className="mdc-button shado-baground-nocolor radius10-font" disabled={true}>
                <span className="mdc-button__label">Start All Video</span>
            </button>
        </If>

        <If condition={!videoVisibleCount}>
            <button
                onClick={() => hostMuteUnmuteAll('VIDEO')}
                className="mdc-button mdc-button--raised shado-baground-textC radius10-font">
                <span className="mdc-button__label">Stop All Video</span>
            </button>
        </If>

    </div>;
};

const mapStateToProps = (state) =>
{
    const audioEnabledCount = Object.values(state.consumers)
        .filter(consumer => Boolean(consumer) && consumer.track.kind === 'audio' &&
            !consumer.locallyPaused && !consumer.remotelyPaused);

    const videoVisibleCount = Object.values(state.consumers)
        .filter(consumer => Boolean(consumer) && consumer.track.kind === 'video' &&
            !consumer.locallyPaused && !consumer.remotelyPaused);

    return {
        audioEnabledCount: audioEnabledCount == 0,
        videoVisibleCount: videoVisibleCount == 0
    };
};

const SideBarUserFooterContainer = withRoomContext(connect(
    mapStateToProps,
    null
)(SideBarUserFooter));


export default SideBarUserFooterContainer;