import React from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import * as appPropTypes from './appPropTypes';
import {withRoomContext} from '../RoomContext';
import * as requestActions from '../redux/requestActions';
import MyDivMain from "./MyDivMain";
import Notifications from "./Notifications";
import {Helmet} from "react-helmet";
import {db} from "../../firebase";
import {withRouter} from "react-router-dom";

class Room extends React.Component
{
	render()
	{
		return (
			<div>
				<Helmet>
					<title>Unlock World</title>
				</Helmet>
				<Notifications/>
				<MyDivMain/>
			</div>
		);
	}

	componentDidMount()
	{
		const {roomClient, invalidMeetingMessage, roomFullMessage} = this.props;
		const meetingId = this.props.room.url.split('/room/')[1].trim();
		const userId = JSON.parse(localStorage.getItem("user")).id;

		db.collection("meeting")
			.where('meetingId', '==', meetingId)
			.get().then(querySnapshot => {

			if (querySnapshot.size !== 0) {
				// roomClient.join();

				db.collection("meeting-participants")
					.where('meetingId', '==', meetingId)
					.where('userId', '==', userId)
					.get().then(querySnapshot => {

					if (querySnapshot.size < 6) {

						db.collection("meeting-participants").add({
							meetingId, userId
						}).then(doc => {
							roomClient.join();
						}).catch(error => console.error("Error adding document: ", error));

					} else {
						roomFullMessage();
						setTimeout(() => {
							this.props.history.push('/');
							// window.location.replace(window.location.origin);
						}, 5000);
					}

				}).catch(error => console.log("Error getting documents: ", error));

			} else {
				invalidMeetingMessage();
				setTimeout(() => {
					this.props.history.push('/');
					// window.location.replace(window.location.origin);
				}, 5000);
			}
		}).catch(error => console.log("Error getting documents: ", error));

	}
}

Room.propTypes =
{
	roomClient      : PropTypes.any.isRequired,
	room            : appPropTypes.Room.isRequired,
	me              : appPropTypes.Me.isRequired,
	amActiveSpeaker : PropTypes.bool.isRequired,
	invalidMeetingMessage  : PropTypes.func.isRequired
};

const mapStateToProps = (state) =>
{
	return {
		room            : state.room,
		me              : state.me,
		amActiveSpeaker : state.me.id === state.room.activeSpeakerId
	};
};

const mapDispatchToProps = (dispatch) =>
{
	return {
		invalidMeetingMessage: () => {
			dispatch(requestActions.notify(
				{
					type: 'error',
					text: 'Please wait for host to start meeting'
				}));
		},
		roomFullMessage: () => {
			dispatch(requestActions.notify(
				{
					type: 'error',
					text: 'Room is full. (maximum 6 participants can join a room)'
				}));
		}
	};
};

const RoomContainer = withRoomContext(connect(
	mapStateToProps,
	mapDispatchToProps
)(Room));

export default withRouter(RoomContainer);
