import React, {useEffect, useState} from 'react';
import RoomClient from "./RoomClient";
import RoomContext from './RoomContext';
import Room from './components/Room';
import {connect} from "react-redux";
import deviceInfo from "./deviceInfo";
import randomString from "random-string";
import * as stateActions from "./redux/stateActions";
import {useParams} from "react-router-dom";

const PreRoom = (props) => {

    const [roomClient, setRoomClient] = useState(null);
    const {setMyself, setUrl, changeDisplayMode, resetTextMessage} = props;
    let { rid } = useParams();

    useEffect(() => {

        const roomId = rid;
        // const roomId = prompt("Enter Room ID",123);

        const peerId = randomString({length: 8}).toLowerCase();

        // const displayName = randomName();
        const displayName = JSON.parse(localStorage.getItem("user")).name;

        const device = deviceInfo();
        const handlerName = undefined;
        const useSimulcast = true;
        const useSharingSimulcast = true;
        const forceTcp = false;
        const produce = true;
        const consume = true;
        const forceH264 = false;
        const forceVP9 = false;
        const svc = undefined;
        const datachannel = true;
        const externalVideo = false;
        const displayNameSet = false;

        setMyself(peerId, displayName, displayNameSet, device);
        setUrl(window.location.href);
        changeDisplayMode('grid');
        resetTextMessage();

        const rc = new RoomClient(
            {
                roomId,
                peerId,
                displayName,
                device,
                handlerName,
                useSimulcast,
                useSharingSimulcast,
                forceTcp,
                produce,
                consume,
                forceH264,
                forceVP9,
                svc,
                datachannel,
                externalVideo
            });
        setRoomClient(rc);

    }, []);

    return <RoomContext.Provider value={roomClient}>
        {roomClient && <Room/>}
    </RoomContext.Provider>;
};

const mapDispatchToProps = (dispatch) =>
{
    return {
        setMyself: (peerId, displayName, displayNameSet, device) => {
            dispatch(stateActions.setMe({peerId, displayName, displayNameSet, device}));
        },
        setUrl: (roomUrl) => dispatch(stateActions.setRoomUrl(roomUrl)),
        changeDisplayMode: (mode) => {
            dispatch(stateActions.changeDisplayMode(mode, null));
        },
        resetTextMessage: () => {
            dispatch(stateActions.resetTextMessage());
        }
    };
};

const PreRoomContainer = connect(
    null,
    mapDispatchToProps)
(PreRoom);

export default PreRoomContainer;