import React, {useState} from "react";

const MirrorButton = () => {

    const [flag, setFlag] = useState(true);

    const transform = () => {
        document.querySelector("a.thumbnail > video").style.transform = flag ? 'scale(-1,1)' : 'scale(1,1)';
        setFlag(!flag);
    };


    return <button type="button"
                   onClick={() => transform()}
                   className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                   data-toggle="tooltip"
                   data-placement="top"
                   title="Mirror"
                   data-original-title="Record">
        {flag ? <i className="material-icons">cached</i> : <i className="material-icons">autorenew</i>}
    </button>;
};

export default MirrorButton;

