import React from "react";
import BottomMenu from "../BottomMenu";
import MeScreenDetails from "./MeScreenDetails";
import {withRoomContext} from "../../RoomContext";
import {connect} from "react-redux";
import {Else, If, Then} from "react-if";
import ScreenDetails from "./ScreenDetails";
import * as stateActions from "../../redux/stateActions";
import speakerView from '../img/speakerview.svg';
import gridView from '../img/grid_view.svg';
import Header from "../Header";

const ScreenView = (props) => {

    const {displayMode, changeDisplayMode, peers, me} = props;

    return <div id="clearfullview" className="container-fuid card clearfullview">
        <div className="col-lg-12 col-md-12 col-lx-12 margin" style={{margin: 'auto', padding: '8px 1% 0px 1%'}}>
            <Header/>
            <div id="drawingboardview" style={{marginTop: 10}}>
                <div className="row ">
                    <div className="col col-lg-12">
                        <div className="row mt-2">
                            <div className="col-md-12">
                                <div className="row">
                                    <div className="col-sm-12 text-right">
                                    <div className="btn-group btn-group-sm c-gray" role="group"
                                         aria-label="Basic example">
                                        <a onClick={() => {
                                            changeDisplayMode('speaker');
                                        }} data-toggle="tooltip" data-placement="top"
                                           title="Spkaer View"><img src={speakerView}/></a>&nbsp;&nbsp;
                                        <a onClick={() => {
                                            changeDisplayMode('grid');
                                        }} data-toggle="tooltip" data-placement="top"
                                           title="List View"><img src={gridView}/></a>
                                    </div></div>
                                </div>
                            </div>
                        </div>

                        <If condition={me.id === displayMode.screenId}>
                            <Then>
                                <MeScreenDetails/>
                            </Then>
                            <Else>
                                <If condition={peers.map(m => (m.id)).includes(displayMode.screenId)}>
                                    <ScreenDetails id={displayMode.screenId}/>
                                </If>
                            </Else>
                        </If>

                        <BottomMenu/>
                    </div>
                </div>
            </div>
        </div>
    </div>;

};

const mapStateToProps = (state) =>
{
    const peersArray = Object.values(state.peers);

    return {
        displayMode: state.displayMode,
        peers: peersArray,
        me: state.me
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        changeDisplayMode: (mode) => {
            dispatch(stateActions.changeDisplayMode(mode, null));
        }
    };
};


const ScreenViewContainer = withRoomContext(connect(
    mapStateToProps,
    mapDispatchToProps
)(ScreenView));

export default ScreenViewContainer;
