import React from "react";
import TopPortion from "./TopPortion";
import BottomMenu from "../BottomMenu";
import MeBottomPortion from "./MeBottomPortion";
import {withRoomContext} from "../../RoomContext";
import {connect} from "react-redux";
import PeerBottomPortion from "./PeerBottomPortion";
import {Else, If, Then} from "react-if";

const LeftPanelPart = (props) => {

    const {activeSpeakerId, amActiveSpeaker} = props;

    return <div className="col-md-9">
        {/*<TopPortion/>*/}
        <If condition={Boolean(activeSpeakerId) === true}>
            <Then>
                <If condition={amActiveSpeaker}>
                    <Then><MeBottomPortion/></Then>
                    <Else><PeerBottomPortion id={activeSpeakerId}/></Else>
                </If>
            </Then>
            <Else>
                <MeBottomPortion/>
            </Else>
        </If>
        <BottomMenu/>
    </div>;
};

const mapStateToProps = (state) => {
    return {
        activeSpeakerId: state.room.activeSpeakerId,
        amActiveSpeaker: state.me.id === state.room.activeSpeakerId
    };
};

const LeftPanelPartContainer = withRoomContext(connect(
    mapStateToProps,
    null
)(LeftPanelPart));


export default LeftPanelPartContainer;
