const initialState = {
    mode: 'list'
};

const rightSideMode = (state = initialState, action) =>
{
    switch (action.type)
    {
        case 'SET_RIGHT_SIDE_MODE':
        {
            const {mode} = action.payload;
            return {...state, mode};
        }

        default:
            return state;
    }
};

export default rightSideMode;