const initialState =
	{
		start: 'inline',
		play: 'none',
		pause: 'none',
		stop: 'none'
	};

const recordingBarUtil = (state = initialState, action) =>
{
	switch (action.type)
	{
		case 'SET_RECORDING_OPERATION':
		{
			const { key, value } = action.payload;
			return {...state, [key]: value};
		}

		default:
			return state;
	}
};

export default recordingBarUtil;
