import React from 'react';
import {connect} from 'react-redux';
import classnames from 'classnames';
import PropTypes from 'prop-types';
import * as appPropTypes from './appPropTypes';
import * as stateActions from '../redux/stateActions';

const Notifications = ({ notifications, onClick }) =>
{
	return (
		<div>
			{
				notifications.map((notification) =>
				{
					return (
						<div  key={notification.id}  id="toast-container" className="toast-top-right" aria-live="polite" role="alert">
							<div
								className={classnames('toast', notification.type === 'info' ? 'toast-success' : 'toast-error')}>
								<div className="toast-message">
									{notification.text}
								</div>
							</div>
						</div>
					);
				})
			}
		</div>
	);
};

Notifications.propTypes =
{
	notifications : PropTypes.arrayOf(appPropTypes.Notification).isRequired,
	onClick       : PropTypes.func.isRequired
};

const mapStateToProps = (state) =>
{
	const { notifications } = state;

	return { notifications };
};

const mapDispatchToProps = (dispatch) =>
{
	return {
		onClick : (notificationId) =>
		{
			dispatch(stateActions.removeNotification(notificationId));
		}
	};
};

const NotificationsContainer = connect(
	mapStateToProps,
	mapDispatchToProps
)(Notifications);

export default NotificationsContainer;
