const initialState = [];

const textMessaging = (state = initialState, action) =>
{
	switch (action.type)
	{
		case 'ADD_MESSAGE':
		{
			const { message } = action.payload;

			return [ ...state, message ];
		}

		case 'RESET_MESSAGE':
		{
			return [];
		}

		default:
			return state;
	}
};

export default textMessaging;
