import React from "react";
import {connect} from "react-redux";

const Header = ({message}) => {
    return (
        <header
            className="MuiPaper-root MuiAppBar-root MuiAppBar-positionFixed MuiAppBar-colorPrimary MuiPaper-elevation4"
            style={{
                backgroundImage: 'linear-gradient(90deg, rgb(255, 115, 107), rgb(255, 34, 96))',
                height: 25
            }}>
            {message}
            <div className="MuiToolbar-root MuiToolbar-regular MuiToolbar-gutters text-right text-light">
                <a id="fullscreen"><i className="fa fa-square-o"/></a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            </div>
        </header>
    );
};

const mapStateToProps = (state) =>
{
    return {
        message            : state.recordingMessage.message
    };
};

const HeaderContainer = connect(
    mapStateToProps,
    null
)(Header);

export default HeaderContainer;