import React, {Component} from "react";
import logo from './conference/logo.png';
import {withRouter} from "react-router-dom";
import {Helmet} from "react-helmet";
import {db} from "./firebase";

class ForgotPassword extends Component {

    constructor(props) {
        super(props);
        this.state = {
            email: null,
            error: "none",
            success: "none"
        };
    }

    mySubmitHandler = event => {
        event.preventDefault();
        let email = this.state.email;

        const re = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
        if (!re.test(email)) {
            this.setState({error: "block"});
            setTimeout(() => { this.setState({error: "none"}) }, 3000);
            return;
        }

        db.collection("users")
            .where('email', '==', email)
            .where('type', '==', 'Normal')
            .get().then(querySnapshot => {

            if (!querySnapshot.empty) {
                querySnapshot.forEach(doc => {
                    const user = {...doc.data()};
                    // console.log(user);

                    let url = 'https://meeting.unlockworld.net/forgotPasswordMail/?' +
                        'email=' + encodeURIComponent(user.email) +
                        '&password=' + encodeURIComponent(user.password) +
                        '&name=' + encodeURIComponent(user.name);

                    this.setState({success: "block"});

                    fetch(url).then(response => response.json()).then(json => {
                        this.setState({success: "none"});
                        this.props.history.push('/signin');
                    });

                });
            } else {
                this.setState({error: "block"});
                setTimeout(() => { this.setState({error: "none"}) }, 3000);
            }

        }).catch(error => console.log("Error getting documents: ", error));
    };

    myChangeHandler = event =>
        this.setState({[event.target.name]: event.target.value});

    homePage = () => this.props.history.push("/");

    render() {
        return (
            <div className="container-full bg-white" style={{height: '100vh'}}>
                <Helmet>
                    <title>Forgot Password</title>
                </Helmet>
                <header
                    className="MuiPaper-root MuiAppBar-root MuiAppBar-positionFixed MuiAppBar-colorPrimary MuiPaper-elevation4"
                    style={{
                        backgroundImage: 'linear-gradient(90deg, rgb(255, 115, 107), rgb(255, 34, 96))',
                        height: 25
                    }}>
                    <div className="MuiToolbar-root MuiToolbar-regular MuiToolbar-gutters"/>
                </header>

                <div style={{display: this.state.error}} id="toast-container" className="toast-top-right"
                     aria-live="polite"
                     role="alert">
                    <div className="toast toast-error">
                        <div className="toast-message">
                            Invalid email or does not exist
                        </div>
                    </div>
                </div>

                <div style={{display: this.state.success}} id="toast-container" className="toast-top-right"
                     aria-live="polite"
                     role="alert">
                    <div className="toast toast-success">
                        <div className="toast-message">
                            Your password has been sent to your email
                        </div>
                    </div>
                </div>

                <div className="row">

                    <form className="border border-light p-5 margin col-md-4" style={{marginTop: '10%'}}
                          onSubmit={this.mySubmitHandler}>
                        <p className="h4 mb-4 text-center">
                            <img style={{width: "35%"}} src={logo} alt="Image" onClick={() => {
                                this.props.history.push('/')
                            }}/>
                        </p>


                        <div className="form-group">
                            <label htmlFor="meetingid">Enter Email/Username</label>
                            <input type="text" id="meetingid" name='email' onChange={this.myChangeHandler}
                                   value={this.state.email || ''} className="form-control mb-6 border4-light"/>
                        </div>

                        <div className="text-center col-sm-12 mb-5">
                            <div className="row">
                                <button type="submit"
                                        className="mdc-button mdc-button--raised shado-baground-textC mt-2 btn-lg"
                                        style={{width: '100%'}}>
                                    <span className="mdc-button__label">
                                        <i className="fa fa-envelope"></i> Retrieve Password</span>
                                </button>
                            </div>
                        </div>
                        <i onClick={this.homePage} className="material-icons">arrow_back</i>
                    </form>

                </div>
            </div>

        );
    }
}

export default withRouter(ForgotPassword);