import React, {Component} from "react";
import {withRoomContext} from "../RoomContext";
import {connect} from 'react-redux';
import PropTypes from "prop-types";
import * as appPropTypes from "./appPropTypes";
import * as stateActions from '../redux/stateActions';
import * as cookiesManager from '../cookiesManager';
import * as requestActions from '../redux/requestActions';
import clipboardCopy from 'clipboard-copy';
import {If} from "react-if";
import {withRouter} from "react-router-dom";
import {db} from "../../firebase";
import BlockUi from 'react-block-ui';
import {Loader} from 'react-loaders';
import 'react-block-ui/style.css';
import 'loaders.css/loaders.min.css';
import BottomRecordingBar from "./BottomRecordingBar";
import MirrorButton from "./MirrorButton";
import BottomPermissionBar from "./BottomPermissionBar";

class BottomMenu extends Component {

    endCall = () => {
        const meetingId = this.props.match.params.rid;
        const userId = JSON.parse(localStorage.getItem("user")).id;
        this.props.showMeetingLoader();

        db.collection("meeting")
            .where('userId', '==', userId)
            .where('meetingId', '==', meetingId)
            .get().then(querySnapshot => {

            if (querySnapshot.size !== 0) {
                this.props.roomClient.endCallForAll().then(() => {
                    this.props.roomClient.close();
                    this.props.history.push('/');
                    // window.location.replace(window.location.origin);
                });
                querySnapshot.forEach((doc) => {
                    db.collection("meeting").doc(doc.id).delete().then(() => {
                        console.log("Document successfully deleted!");
                        // window.location.replace(window.location.origin);
                    }).catch(error => {
                        console.error("Error removing document: ", error);
                    });
                });
            } else {
                this.props.roomClient.close();
                this.props.history.push('/');
                // window.location.replace(window.location.origin);
            }

        }).catch(error => console.log("Error getting documents: ", error));
    };

  render() {

      const {
          roomClient,
          connected,
          me,
          audioProducer,
          videoProducer,
          faceDetection,
          onSetStatsPeerId,
          onRoomLinkCopy,
          comingSoon,
          room,
          changeDisplayMode,
          displayMode,
          changeRightSideMode,
          rightSideMode,
          meetingLoader
      } = this.props;

      const copyRoomUrl = 'Unlock World\nMake Your Life Easier with Unlock!\n' +
          'https://meeting.unlockworld.net/room/' + this.props.match.params.rid +
          '\nRoom ID: ' + this.props.match.params.rid;

    let micState;

    if (!me.canSendMic)
      micState = 'unsupported';
    else if (!audioProducer)
      micState = 'unsupported';
    else if (!audioProducer.paused)
      micState = 'on';
    else
      micState = 'off';

    let webcamState;

    if (!me.canSendWebcam)
      webcamState = 'unsupported';
    else if (videoProducer && videoProducer.type !== 'share')
      webcamState = 'on';
    else
      webcamState = 'off';

    let changeWebcamState;

    if (Boolean(videoProducer) && videoProducer.type !== 'share' && me.canChangeWebcam)
      changeWebcamState = 'on';
    else
      changeWebcamState = 'unsupported';

    let shareState;

    if (Boolean(videoProducer) && videoProducer.type === 'share')
      shareState = 'on';
    else
      shareState = 'off';

    const videoVisible = Boolean(videoProducer) && !videoProducer.paused;

    const loaderStyle = {position: "fixed", top: "50%", left: "50%", transform: "translate(-50%, -50%)"};

      return (

          <div className="col-md-12 col-sm-12">
              <BlockUi
                  renderChildren={false}
                  tag="div"
                  blocking={meetingLoader.visible}
                  loader={<Loader style={loaderStyle} active type={"semi-circle-spin"} color="#FF5549"/>}
              >
                  <div className="row">
                      <nav className="navbar navbar-expand-lg navbar-dark scrolling-navbar overright-nabbar nopadding">


                          <div className="col-12 show-mobail">
                              <div className="row">
                                  <div className="col-2 text-left">
                                      <button
                                          className="navbar-toggler"
                                          type="button"
                                          data-toggle="collapse"
                                          data-target=""
                                          aria-controls=""
                                          aria-expanded="false"
                                          aria-label="Toggle navigation"
                                      >
                                          <i className="material-icons c-text">menu</i>
                                      </button>
                                  </div>
                                  <div className="col-8 text-center">
                                      <a className="btn-floating" onClick={() => {
                                          micState === 'on'
                                              ? roomClient.muteMic()
                                              : roomClient.unmuteMic();
                                      }}>
                                          {(micState === 'on') ? <i className="material-icons">mic_none</i> :
                                              <i className="material-icons">mic_off</i>}
                                      </a>

                                      <a className="btn-floating p-1"
                                         onClick={() => {
                                             if (webcamState === 'on') {
                                                 cookiesManager.setDevices({webcamEnabled: false});
                                                 roomClient.disableWebcam();
                                             } else {
                                                 cookiesManager.setDevices({webcamEnabled: true});
                                                 roomClient.enableWebcam();
                                             }
                                         }}>
                                          {(webcamState === 'on') ? <i className="material-icons">videocam</i> :
                                              <i className="material-icons">videocam_off</i>}
                                      </a>

                                      <a className="btn-floating  p-1"
                                         onClick={(event) => {
                                             // If this is a 'Open in new window/tab' don't prevent
                                             // click default action.
                                             if (
                                                 event.ctrlKey || event.shiftKey || event.metaKey ||
                                                 // Middle click (IE > 9 and everyone else).
                                                 (event.button && event.button === 1)
                                             ) {
                                                 return;
                                             }

                                             event.preventDefault();

                                             clipboardCopy(copyRoomUrl)
                                                 .then(onRoomLinkCopy);
                                         }}>
                                          <i className="material-icons ">person_add_alt_1</i>
                                      </a>

                                      <If condition={displayMode.mode === 'grid'}>
                                          <a className="btn-floating p-1"
                                             onClick={() => {
                                                 changeDisplayMode('speaker');
                                             }}>
                                              <span className="material-icons">insert_comment</span>
                                          </a>
                                      </If>

                                      <If condition={displayMode.mode === 'speaker'}>
                                          <a className="btn-floating p-1"
                                             onClick={() => {
                                                 changeDisplayMode('grid');
                                             }}>
                                              <span className="material-icons">grid_on</span>
                                          </a>
                                      </If>

                                      <If condition={displayMode.mode === 'screen'}>
                                          <a className="btn-floating p-1"
                                             onClick={() => {
                                                 changeDisplayMode('grid');
                                             }}>
                                              <span className="material-icons">grid_on</span>
                                          </a>
                                      </If>

                                      <If condition={displayMode.mode === 'speaker'}>
                                          <a className="btn-floating p-1"
                                             onClick={() => {
                                                 changeRightSideMode(rightSideMode.mode === 'chat' ? 'list' : 'chat');
                                             }}>
                                              <If condition={rightSideMode.mode === 'chat'}>
                                                  <i className="material-icons">people_alt</i>
                                              </If>
                                              <If condition={rightSideMode.mode === 'list'}>
                                                  <span className="material-icons">chat</span>
                                              </If>
                                          </a>
                                      </If>
                                  </div>
                                  <div className="col-2 text-right">
                                      <div className="row">
                                          <a
                                              onClick={() => this.endCall()}
                                              className="btn-floating btn btn-danger btn-sm shado-baground-textC"
                                              style={{
                                                  padding: "5px",
                                                  marginTop: "0px"
                                              }}
                                          >
                                              End Call
                                          </a>
                                      </div>

                                  </div>
                              </div>


                          </div>

                          <div
                              className="collapse navbar-collapse"
                              id="navbarSupportedContent-9"
                          >
                              <div className="row grad-ation w-100">
                                  <div className="col-md-3 nopadding text-center group-btn-bottom">

                                      <button
                                          type="button"
                                          className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                                          title="Audio"
                                          data-toggle="tooltip"
                                          data-placement="top"
                                          onClick={() => {
                                              micState === 'on'
                                                  ? roomClient.muteMic()
                                                  : roomClient.unmuteMic();
                                          }}
                                      >
                                          {(micState === 'on') ? <span className="material-icons">mic_none</span> :
                                              <span className="material-icons">mic_off</span>}
                                      </button>

                                      <button
                                          type="button"
                                          className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                                          data-toggle="tooltip"
                                          data-placement="top"
                                          title="Video"
                                          disabled={me.webcamInProgress || me.shareInProgress}
                                          onClick={() => {
                                              if (webcamState === 'on') {
                                                  cookiesManager.setDevices({webcamEnabled: false});
                                                  roomClient.disableWebcam();
                                              } else {
                                                  cookiesManager.setDevices({webcamEnabled: true});
                                                  roomClient.enableWebcam();
                                              }
                                          }}
                                      >
                                          {(webcamState === 'on') ? <span className="material-icons">videocam</span> :
                                              <span className="material-icons">videocam_off</span>}
                                      </button>

                                  </div>
                                  <div className="col-md-7 nopadding text-center group-btn-bottom">

                                      {/*<BottomPermissionBar/>*/}

                                      <button
                                          type="button"
                                          className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                                          data-tooltip="tooltip"
                                          data-placement="top"
                                          title="Invite"
                                          onClick={(event) => {
                                              // If this is a 'Open in new window/tab' don't prevent
                                              // click default action.
                                              if (
                                                  event.ctrlKey || event.shiftKey || event.metaKey ||
                                                  // Middle click (IE > 9 and everyone else).
                                                  (event.button && event.button === 1)
                                              ) {
                                                  return;
                                              }

                                              event.preventDefault();

                                              /*clipboardCopy(room.url)
                                                  .then(onRoomLinkCopy);*/
                                              clipboardCopy(copyRoomUrl)
                                                  .then(onRoomLinkCopy);
                                          }}
                                      >
                                          <i className="material-icons">person_add_alt_1</i>
                                      </button>

                                      <button
                                          type="button"
                                          className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm active"
                                          data-toggle="tooltip"
                                          data-placement="top"
                                          title="Share Screen"
                                          disabled={me.shareInProgress || me.webcamInProgress}
                                          onClick={() => {
                                              /*if (shareState === 'on')
                                                  roomClient.disableShare();
                                              else
                                                  roomClient.enableShare();*/
                                              comingSoon();
                                          }}
                                      >
                                          <i className="material-icons">screen_share</i>
                                      </button>

                                      <If condition={displayMode.mode === 'grid'}>
                                          <button
                                              type="button"
                                              className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                                              data-toggle="tooltip"
                                              data-placement="top"
                                              title="Chat"
                                              onClick={() => {
                                                  changeDisplayMode('speaker');
                                              }}
                                          >
                                              <i className="fa fa-commenting-o" aria-hidden="true"/>
                                          </button>
                                      </If>


                                      <If condition={displayMode.mode === 'speaker'}>
                                          <button
                                              type="button"
                                              className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                                              data-toggle="tooltip"
                                              data-placement="top"
                                              title="Grid"
                                              onClick={() => {
                                                  changeDisplayMode('grid');
                                              }}
                                          >
                                              <i className="fa fa-th-large" aria-hidden="true"/>
                                          </button>
                                      </If>

                                      <If condition={displayMode.mode === 'screen'}>
                                          <button
                                              type="button"
                                              className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                                              data-toggle="tooltip"
                                              data-placement="top"
                                              title="Chat"
                                              onClick={() => {
                                                  changeDisplayMode('speaker');
                                              }}
                                          >
                                              <i className="fa fa-commenting-o" aria-hidden="true"/>
                                          </button>
                                      </If>

                                      <If condition={displayMode.mode === 'speaker'}>
                                          <button
                                              type="button"
                                              className="btn btn-danger bmd-btn-fab bmd-btn-fab-sm"
                                              data-toggle="tooltip"
                                              data-placement="top"
                                              title="Chat"
                                              onClick={() => {
                                                  changeRightSideMode(rightSideMode.mode === 'chat' ? 'list' : 'chat');
                                              }}
                                          >


                                              <If condition={rightSideMode.mode === 'chat'}>
                                                  <i className="material-icons">people_alt</i>
                                              </If>
                                              <If condition={rightSideMode.mode === 'list'}>
                                                  <i className="fa fa-commenting-o" aria-hidden="true"/>
                                              </If>
                                          </button>
                                      </If>

                                      <BottomRecordingBar/>

                                      <If condition={displayMode.mode === 'grid'}>
                                      <MirrorButton/>
                                      </If>

                                  </div>
                                  <div className="col-md-2 nopadding text-center">
                                      <button
                                          onClick={() => this.endCall()}
                                          className="btn-floating btn btn-danger btn-sm shado-baground-textC btn-rounded end-call-round">
                                          <span className="mdc-button__label">End Call</span>
                                      </button>
                                  </div>

                              </div>
                          </div>

                      </nav>

                  </div>
              </BlockUi>
          </div>
      );
  }
}

BottomMenu.propTypes =
    {
      roomClient       : PropTypes.any.isRequired,
      connected        : PropTypes.bool.isRequired,
      me               : appPropTypes.Me.isRequired,
      audioProducer    : appPropTypes.Producer,
      videoProducer    : appPropTypes.Producer,
      faceDetection    : PropTypes.bool.isRequired,
      onSetStatsPeerId : PropTypes.func.isRequired
    };

const mapStateToProps = (state) => {
    const producersArray = Object.values(state.producers);
    const audioProducer =
        producersArray.find((producer) => producer.track.kind === 'audio');
    const videoProducer =
        producersArray.find((producer) => producer.track.kind === 'video');

    return {
        connected: state.room.state === 'connected',
        me: state.me,
        room: state.room,
        audioProducer: audioProducer,
        videoProducer: videoProducer,
        faceDetection: state.room.faceDetection,
        onRoomLinkCopy: PropTypes.func.isRequired,
        comingSoon: PropTypes.func.isRequired,
        changeDisplayMode: PropTypes.func.isRequired,
        displayMode            : state.displayMode,
        rightSideMode            : state.rightSideMode,
        meetingLoader            : state.meetingLoader
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onSetStatsPeerId: (peerId) => dispatch(stateActions.setRoomStatsPeerId(peerId)),
        onRoomLinkCopy: () => {
            dispatch(requestActions.notify(
                {
                    text: 'Room link copied to the clipboard'
                }));
        },
        comingSoon: () => {
            dispatch(requestActions.notify(
                {
                    type: 'error',
                    text: 'Coming soon...'
                }));
        },
        changeDisplayMode: (mode) => {
            dispatch(stateActions.changeDisplayMode(mode, null));
        },
        changeRightSideMode: (mode) => {
            dispatch(stateActions.changeRightSideMode(mode));
        },
        showMeetingLoader: () => {
            dispatch(stateActions.changeMeetingLoaderState(true));
        }
    };
};

const BottomMenuContainer = withRoomContext(connect(
    mapStateToProps,
    mapDispatchToProps
)(BottomMenu));

export default withRouter(BottomMenuContainer);
