import React from "react";
import SidebarMember from "./SidebarMember";
import PropTypes from "prop-types";
import * as appPropTypes from "../appPropTypes";
import {connect} from "react-redux";
import {If} from "react-if";
import SidebarMemberMe from "./SidebarMemberMe";
import SideBarUserFooter from "./SideBarUserFooter";

const SidebarUserList = (props) => {

    const {peers, me} = props;

    const [search, setSearch] = React.useState("");
    const [members, setMembers] = React.useState([]);

    React.useEffect(() => {
        let map = peers.map(m => ({displayName: m.displayName, id: m.id}));

        if (Boolean(search)) {
            setMembers(map.filter(m => m.displayName.toLowerCase().includes(search.toLowerCase())));
        } else {
            setMembers(map);
        }

    }, [peers, search]);

    return <div className="col-md-3 nopadding">
        <div className="search-widget border-left rounded-left mon-border-4">
            <div className="search-from-wiget">
                <form className="form-inline d-flex justify-content-center md-form form-sm m-0">
                    <i
                        className="fa fa-search "
                        aria-hidden="true"
                        style={{
                            float: "left",
                            width: "10%",
                            color: "#fff"
                        }}
                    />
                    <input
                        className="input-wigate-serach"
                        type="text"
                        style={{
                            width: "80%",
                            float: "right",
                            border: "0px",
                            color: "#fff"
                        }}
                        placeholder="Find a Participant"
                        aria-label="Search"
                        onChange={(e) => setSearch(e.target.value)}
                    />
                </form>
            </div>
            <div className="user-list">
                <ul className="list-unstyled friend-list">
                    <If condition={me.displayName.toLowerCase().includes(search.toLowerCase())}>
                        <SidebarMemberMe displayName={me.displayName} key={me.id}/>
                    </If>
                    {members.map(member => <SidebarMember
                        displayName={member.displayName} key={member.id} id={member.id}/>)}
                </ul>
            </div>
            <SideBarUserFooter/>
        </div>
    </div>;
};

SidebarUserList.propTypes =
    {
        peers           : PropTypes.arrayOf(appPropTypes.Peer).isRequired
    };

const mapStateToProps = (state) =>
{
    const peersArray = Object.values(state.peers);

    return {
        peers           : peersArray,
        me              : state.me
    };
};

const SidebarUserListContainer = connect(
    mapStateToProps,
    null
)(SidebarUserList);




export default SidebarUserListContainer;

