import React from "react";

const SidebarMemberMe = props => {

    const truncatedText = str => {
        let length = 18;
        let ending = '...';
        return str.length > length ? str.substring(0, length - ending.length) + ending : str;
    };

    return <li>
        <a className="d-flex justify-content-start">
            <span className="material-icons">person_pin</span>
            <div className="w-75">
                <strong>&nbsp;{truncatedText(props.displayName) + ' (Me)'}</strong>
            </div>
            <div className="chat-footer">
                <span className=" float-right"/>
            </div>
        </a>
    </li>;
};

export default SidebarMemberMe;