import React from "react";
import SiderBarFooter from "./SiderBarFooter";
import {connect} from "react-redux";
import classnames from 'classnames';

const ChatSidebar = (props) => {

    return (
        <div className="col-md-3 nopadding">
            <div className="search-widget w-100 border-left rounded-left mon-border-4">
                <div className="sider-barheader">Group Chat</div>

                <div className="message-history nopadding col-md-12">

                    {props.textMessaging.sort((a, b) => (a.time > b.time) ? 1 : -1)
                        .map((obj, i) => <p key={i} className={classnames(obj.isMe === true && 'rightText text-right')}
                                            style={{textAlign:obj.isMe !== true && 'left'}}>
                            <span>{obj.sender}: {obj.isMe === true && obj.message}</span>
                            {obj.isMe === false && obj.message}
                        </p>)}
                </div>

                <SiderBarFooter/>
            </div>
        </div>
    );
};

const mapStateToProps = (state) => {
    return {
        textMessaging: state.textMessaging
    };
};

const ChatSidebarContainer = connect(
    mapStateToProps,
    null
)(ChatSidebar);

export default ChatSidebarContainer;
